/*
Poset

Poset, or partially ordered set.

The purpose of this entity was to group other entities, including other posets.

This was supposed to be a generalization of folders, and in fact, it works
very similarly to directed-acyclic graphs (DAGs) with the exception that there
is only one root node, i.e. only one node that has no parents, and there's a
path from this root node to all other nodes of the graph by only traversing 
through the children.
*/

import { GTMState } from "../gtm-lib-common";
import { assert, hasOwnProperty } from "../utils";

export function createPoset(state: GTMState) {
  throw new Error("Not implemented!");
}

export function getPoset(state: GTMState, id: string) {
  assert(hasOwnProperty(state.entities, id));
  const entity = state.entities[id];
  assert(entity.type === "poset");
  return entity;
}

