import React from "react";

import HCFLayout from "../../layout/hcf";

import { download, copyToClipboard } from "../../utils-ui";
import { Navi } from '../../navi';

import { GTMApp } from "../../gtm-app/gtm";

import "./index.css";

interface Props {
  gtm: GTMApp;
  navi: Navi;
}

/**
 * URL: `gtm:/export`
 * 
 * @param props 
 */
const PageExport: React.FC<Props> = (props) => {
  const { gtm, navi } = props;
  
  const export_value = gtm.common.doExport();

  // ---------------------------------------------------------------------------
  // Header
  const header = React.createElement(
    "div",
    {
      key: "1",
      className: "topBar",
    },
    React.createElement(
      "div",
      {},
      React.createElement(
        "button",
        {
          onClick: () => {
            navi.back();
          },
        },
        "Go back"
      )
    )
  );

  // ---------------------------------------------------------------------------
  // Content
  const content = React.createElement(
    "div",
    {
      key: "2",
      style: {
        overflowWrap: "break-word",
      },
    },
    React.createElement(
      "span",
      {
        style: {
          fontFamily: "monospace",
        },
      },
      export_value
    )
  );

  // ---------------------------------------------------------------------------
  // Footer
  const footer = React.createElement(
    "div",
    {
      key: "3",
      style: {
        display: "flex",
        justifyContent: "space-between",
      },
      className: "bottomBar",
    },
    React.createElement(
      "button",
      {
        onClick: () => {
          download(`export-${Date.now()}.txt`, export_value);
        },
      },
      "Download"
    ),
    React.createElement(
      "button",
      {
        onClick: () => {
          copyToClipboard(export_value);
        },
      },
      "Copy to clipboard"
    )
  );

  return (
    <HCFLayout>
      {header}
      {content}
      {footer}
    </HCFLayout>
  );
}

export default PageExport;
