import { GTMState, GTMEntityDependency, getNextID } from "../gtm-lib-common";
import { assert, hasOwnProperty } from "../utils";

// NOTE: don't use yet!

export function createDependency(state: GTMState, source_id: string, target_id: string) {
  {
    assert(state.entities.hasOwnProperty(source_id));
    assert(state.entities.hasOwnProperty(target_id));

    const source = state.entities[source_id];
    const target = state.entities[target_id];

    assert(source.type === "task");
    assert(target.type === "task");
  }

  // TODO: try to look up dependency before creating a new one.

  // Create new dependency
  const id = getNextID(state);

  const now_json = (new Date()).toJSON();

  const dependency: GTMEntityDependency = {
    type: "dependency",
    id,

    date_created: now_json,

    source_id,
    target_id,
  };

  // Add dependency to state
  // state.entities[id] = dependency;

  // Return dependency
  return dependency;
}


export function getDependency(state: GTMState, id: string) {
  assert(hasOwnProperty(state.entities, id));
  const entity = state.entities[id];
  assert(entity.type === "dependency");
  return entity;
}

