/*
Storka
*/

import { ObservableSlim, TObserver } from "./observable-slim";


// NOTE: when automatic synchronization kicks in, make sure not to throw away
// partial transactions.  That is _only_ OK when starting Storka for the first
// time.

////////////////////////////////////////////////////////////////////////////////
// Helper functions

/**
 * Asserts the condition `condition`.  If the value is not boolean then it is
 * cast to a boolean value.  If the condition evaluates to a falsy value then
 * an exception is raised.  An optional message `message` is displayed and
 * other optional things `...other` printed to the error console.
 * 
 * // TODO: in future version ...): asserts condition { ...
 * can be added.
 *
 * @param {any} condition
 * @param {string=} message
 * @param {any=} other
 *
 * @throws Will throw an error if `condition` is falsy.
 */
export function assert(
  condition: any,
  message = "Assertion failed",
  ...other: any[]
): asserts condition {
  if (!condition) {
    if (typeof message !== "string") {
      console.error(JSON.parse(JSON.stringify(message)));
      throw new Error("Assertion failed");
    } else {
      if (other.length > 0) {
        console.error(JSON.parse(JSON.stringify(other)));
      }
    }
    throw new Error(message);
  }
}

////////////////////////////////////////////////////////////////////////////////
// Storka events

export type StorkaEventAfterAddChange = { type: "afteraddchange", change: ChangeItem, object: any };
export type StorkaEventBeforeAddChange = { type: "beforeaddchange", change: ChangeItem, object: any };
export type StorkaEventSyncing = { type: "syncing" };
export type StorkaEventSynced = { type: "synced" };
export type StorkaEventReady = { type: "ready" };
export type StorkaEventChange = { type: "change", change: ChangeItem, object: any };

/** To do something before a change is added. */
export type StorkaEventBeforeChange = { type: "beforechange", change: ChangeItem, object: any };

/** To do something after a change is added. */
export type StorkaEventAfterChange = { type: "afterchange", change: ChangeItem, object: any };

export type StorkaEvent = StorkaEventSyncing | StorkaEventSynced | StorkaEventReady | StorkaEventChange | StorkaEventBeforeChange | StorkaEventAfterChange | StorkaEventBeforeAddChange | StorkaEventAfterAddChange;

////////////////////////////////////////////////////////////////////////////////
// Storka event types

type StorkaEventType = "syncing" | "synced" | "ready" | "change" | "beforechange" | "afterchange" | "beforeaddchange" | "afteraddchange";

////////////////////////////////////////////////////////////////////////////////
// Storka event listeners

type StorkaEventListener = (ev: StorkaEvent) => void;

interface StorkaListeners {
  afterchange: Set<StorkaEventListener>,
  beforechange: Set<StorkaEventListener>,
  change: Set<StorkaEventListener>,
  ready: Set<StorkaEventListener>,
  synced: Set<StorkaEventListener>,
  syncing: Set<StorkaEventListener>,
  beforeaddchange: Set<StorkaEventListener>,
  afteraddchange: Set<StorkaEventListener>,
}

////////////////////////////////////////////////////////////////////////////////
// Various Storka types

interface StorkaReturnObject {
  object: any;
  proxy: any;
  addEventListener: (type: StorkaEventType, listener: StorkaEventListener) => void;
  removeEventListener: (type: StorkaEventType, listener: StorkaEventListener) => void;
  ready: boolean;
  beginTransaction: () => void,
  endTransaction: () => void,
}

type ChangeItem = { type: "set", jsonPointer: string, value: any } | { type: "delete", jsonPointer: string } | { type: "begin-transaction", id: string } | { type: "end-transaction", id: string };


////////////////////////////////////////////////////////////////////////////////
// Scheduler types

type TJobHandlerCallback = (result: { ok: true } | { ok: false, reason: string }) => void;
type TJobHandler = (callback: TJobHandlerCallback) => void;

////////////////////////////////////////////////////////////////////////////////


/**
 * Creates a "scheduler"
 */
function createScheduler() {

  const jobTypes = new Set<string>();

  // NOTE: not optimal
  const jobQueue: string[] = [];

  const jobHandlers = new Map<string, TJobHandler>();

  let working = false;

  function addJobType(jobType: string) {
    assert(!jobTypes.has(jobType));
    jobTypes.add(jobType);
  }

  function setJobHandler(jobType: string, handler: TJobHandler) {
    assert(!jobHandlers.has(jobType));
    jobHandlers.set(jobType, handler);
  }

  function removeJobHandler(jobType: string) {
    throw new Error("Not implemented")
  }

  function scheduleJob(jobType: string) {
    assert(jobTypes.has(jobType));

    if (!jobQueue.includes(jobType)) jobQueue.push(jobType);

    if (!working) {
      working = true;
      dispatch();
    }
  }

  const handleJobHandlerCallback: TJobHandlerCallback = (result) => {
    if (result.ok) {
      assert(working, "Don't konw what is going on...");
      dispatch();
    } else {
      throw new Error(result.reason);
    }
  };

  function dispatch() {
    if (jobQueue.length === 0) {
      // Done
      working = false;
      return;
    }

    const jobType = jobQueue.shift() as string;

    assert(jobHandlers.has(jobType));

    const jobHandler = jobHandlers.get(jobType) as TJobHandler;

    jobHandler(handleJobHandlerCallback);
  }

  return {
    addJobType,
    setJobHandler,
    scheduleJob,
    removeJobHandler,
  };
}


/**
 * Creates a Storka object using IndexeDB database `database_name`.
 * 
 * @param database_name 
 * @param config 
 */
export function createStorka(database_name: string, config: { transactional: boolean }) {

  if (!window.indexedDB)
    throw new Error("Unable to use application since your browser does not support a stable version of IndexedDB.");

  let object: any = {}; // The object (state) we're going to proxy.
  let proxy: any = {};  // The proxy for the object (state).

  let { transactional = false } = config;

  const scheduler = createScheduler();
  scheduler.addJobType("applychanges");
  scheduler.addJobType("addchanges");

  const listeners: StorkaListeners = {
    afterchange: new Set<StorkaEventListener>(),
    beforechange: new Set<StorkaEventListener>(),
    change: new Set<StorkaEventListener>(),
    synced: new Set<StorkaEventListener>(),
    syncing: new Set<StorkaEventListener>(),
    ready: new Set<StorkaEventListener>(),
    beforeaddchange: new Set<StorkaEventListener>(),
    afteraddchange: new Set<StorkaEventListener>(),
  };

  let is_ready = false; // Becomes `true` when it's OK to use.

  let pending_changes: ChangeItem[] = [];

  // Accumulation of data
  let accumulatedLocalChange = 0;
  let accumulatedDbChange = 0;

  const ACCUMULATED_CHANGE_LIMIT = 20 * 1024 * 1024; // 20 MB

  //////////////////////////////////////////////////////////////////////////////
  // Various

  /**
   * Extract path elements from JSON pointer `jsonPointer`.
   * 
   * @param {string} jsonPointer 
   */
  function getPathFromJsonPointer(jsonPointer: string) {
    return jsonPointer.split("/").filter(x => x.length > 0);
  }

  /**
   * Applies the change `change` to object `object`.
   * 
   * @param change 
   * @param object 
   * 
   * @throws
   */
  function applyChangeToObject(change: ChangeItem, object: any) {
    assert(change.type === "set" || change.type === "delete");

    const path = getPathFromJsonPointer(change.jsonPointer);

    assert(path.length > 0);

    let node = object;

    for (let i = 0; i < path.length - 1; i++) node = node[path[i]];

    const last_crumb = path[path.length - 1];

    if (change.type === "set") node[last_crumb] = change.value;
    else if (change.type === "delete") delete node[last_crumb];
    else throw new Error();
  }


  //////////////////////////////////////////////////////////////////////////////
  // Database stuff

  /**
   * Initialize IndexedDB database.  This function executed every time when
   * setting up Storka.
   * 
   * Returns `true` if this is the first time this function is called,
   * i.e. the database is being created, otherwise `false`.
   */
  function dbInit(): Promise<boolean> {
    return new Promise((resolve, reject) => {
      let upgrading = false;

      // TODO: the code here is a total brittle mess!!!

      // Going to wait `timeout` milliseconds, in case `upgradeneeded` event is
      // is fired after `success` event is fired.
      const potential_onupgradeneeded_timeout = 50;

      const onupgradedneeded_done_timeout = 20;

      const req = window.indexedDB.open(database_name);
      req.onblocked = (e) => reject(`Blocked when opening database ${database_name}.  It's maybe in use.`);
      // NOTE: (2020-02-27 15:04) IndexedDB does not work in private browsing
      // mode in Firefox.
      req.onerror = (e) => reject(`Error when opening database ${database_name}`);
      req.onupgradeneeded = (e) => {
        upgrading = true;

        const db = req.result;

        // req.transaction

        // TODO: the code here below is super brittle
        db.createObjectStore("object", {});
        db.createObjectStore("changes", { keyPath: "key", autoIncrement: true });

        setTimeout(() => {
          resolve(true);
        }, onupgradedneeded_done_timeout);
      };

      req.onsuccess = (e) => {
        // TODO: potentially brittle
        setTimeout(() => {
          if (!upgrading) {
            const db = req.result;
            db.close();
            resolve(false);
          }
        }, potential_onupgradeneeded_timeout);
      };
    });
  }

  /**
   * The first time this application is executed an object store is created for
   * the object and the change list.  After creating a an object, this function
   * is executed to initialize the object in the object store.  The initial
   * value is `{}`.
   */
  function dbInitObject(): Promise<void> {
    return new Promise((resolve, reject) => {
      const req = window.indexedDB.open(database_name);
      req.onblocked = (e) => reject();
      req.onerror = (e) => reject();
      req.onupgradeneeded = (e) => reject();
      req.onsuccess = (e) => {
        const db = req.result;

        const transaction = db.transaction(["object"], "readwrite");
        transaction.oncomplete = (e) => { resolve(); };
        transaction.onerror = (e) => { reject(); };

        const store = transaction.objectStore("object");

        const store_req = store.put({}, 0);
        store_req.onerror = (e) => {
          reject();
        };
      };
    });
  }

  /**
   * Adds a list of changes to apply to the object to the object store
   * `changes`.
   * 
   * @param items 
   */
  function dbAddChanges(items: ChangeItem[]) {
    return new Promise((resolve, reject) => {
      const req = window.indexedDB.open(database_name);
      req.onblocked = (e) => { reject(); };
      req.onerror = (e) => { reject(); };
      req.onupgradeneeded = (e) => { reject(); };
      req.onsuccess = (e) => {
        const db = req.result;

        const transaction = db.transaction(["changes"], "readwrite");
        transaction.oncomplete = (e) => { resolve(); };
        transaction.onerror = (e) => { reject(); };

        const store = transaction.objectStore("changes");

        items.forEach(function (item) {
          const store_req = store.add(item);
          store_req.onsuccess = (e) => { };
          store_req.onerror = (e) => { reject(); };
        });
      };
    });
  }

  /**
   * Retrieves all changes from object store `changes`.
   */
  function dbGetChanges(): Promise<ChangeItem[]> {
    return new Promise((resolve, reject) => {
      let changes: any;

      // Retrieveing changes
      const req = window.indexedDB.open(database_name);
      req.onblocked = (e) => { reject(); };
      req.onerror = (e) => { reject(); };
      req.onupgradeneeded = (e) => { reject(); };
      req.onsuccess = (e) => {
        const db = req.result;

        const transaction = db.transaction(["changes"], "readonly");
        transaction.oncomplete = (e) => { resolve(changes); };
        transaction.onerror = (e) => { reject(); };

        const store = transaction.objectStore("changes");

        const store_req = store.getAll();
        store_req.onerror = (e) => { };
        store_req.onsuccess = (e) => {
          // @ts-ignore
          const result = e.target.result;
          changes = result;
        };
      };
    });
  }

  /**
   * Removes all changes from changes object store.
   */
  function dbRemoveAllChanges(): Promise<void> {
    return new Promise((resolve, reject) => {
      // Retrieveing changes
      const req = window.indexedDB.open(database_name);
      req.onblocked = (e) => { reject(); };
      req.onerror = (e) => { reject(); };
      req.onupgradeneeded = (e) => { reject(); };
      req.onsuccess = (e) => {
        const db = req.result;

        const transaction = db.transaction(["changes"], "readwrite");
        transaction.oncomplete = (e) => { resolve(); };
        transaction.onerror = (e) => { reject(); };

        const store = transaction.objectStore("changes");

        const store_req = store.clear();
        store_req.onerror = (e) => { reject(); };
        store_req.onsuccess = (e) => { };
      };
    });
  }

  /**
   * Retrieves the changes from the changes object store and the object from
   * the object object store, and applies the changes to the object, removes
   * the changes from the changes object store, and sets the update object to
   * the object object store.
   * 
   * TODO: it should work in a different manner when this is run the first time
   * and other times.
   */
  async function dbApplyChanges(): Promise<void> {
    const values: [ChangeItem[], any] = await Promise.all([
      dbGetChanges(),
      dbGetObject(),
    ]);

    const [changes, object] = values;

    if (changes.length > 0) {
      if (transactional) {
        const transactions: ChangeItem[][] = [];
        let transaction: ChangeItem[] = [];

        let inTransaction = false;
        let transactionID = null;

        for (let i = 0; i < changes.length; i++) {
          const change = changes[i];

          if (inTransaction) {
            assert(change.type !== "begin-transaction");

            if (change.type === "end-transaction") {
              assert(change.id === transactionID);

              transactions.push(transaction);
              transaction = [];

              transactionID = null;
              inTransaction = false;
            } else {
              transaction.push(change);
            }
          } else {
            assert(transaction.length === 0);
            assert(change.type === "begin-transaction");
            assert(transactionID === null);

            transactionID = change.id;
            inTransaction = true;
          }
        }

        assert(!inTransaction);
        assert(transaction.length === 0);
        assert(transactions.length > 0);

        // Apply transactions

        for (const transaction of transactions) {
          for (const change of transaction) {
            applyChangeToObject(change, object);
          }
        }

        await dbSetObject(object);
        await dbRemoveAllChanges();
      } else {
        throw new Error("Not implemented");
      }
    }

    return;
  }

  /**
   * Retrieves the object from the object store.
   */
  function dbGetObject(): Promise<any> {
    return new Promise((resolve, reject) => {
      let object: any;

      // Retrieveing changes
      const req = window.indexedDB.open(database_name);
      req.onblocked = (e) => { reject(); };
      req.onerror = (e) => { reject(); };
      req.onupgradeneeded = (e) => { reject(); };
      req.onsuccess = (e) => {
        const db = req.result;

        const transaction = db.transaction(["object"], "readonly");
        transaction.oncomplete = (e) => { resolve(object); };
        transaction.onerror = (e) => { reject(); };

        const store = transaction.objectStore("object");

        const store_req = store.get(0);
        store_req.onerror = (e) => { };
        store_req.onsuccess = (e) => {
          // @ts-ignore
          const result = e.target.result;
          object = result;
        };
      };
    });
  }

  /**
   * Sets the object in the object store.
   * 
   * @param object 
   */
  function dbSetObject(object: any): Promise<void> {
    return new Promise((resolve, reject) => {
      const req = window.indexedDB.open(database_name);
      req.onblocked = (e) => { reject(); };
      req.onerror = (e) => { reject(); };
      req.onupgradeneeded = (e) => { reject(); };
      req.onsuccess = (e) => {
        const db = req.result;

        const transaction = db.transaction(["object"], "readwrite");
        transaction.oncomplete = (e) => { resolve(); };
        transaction.onerror = (e) => { reject(); };

        const store = transaction.objectStore("object");

        const store_req = store.put(object, 0);
        store_req.onerror = (e) => {
          reject();
        };
      };
    });
  };

  /**
   * Call this function at start
   */
  async function dbSetup() {
    // Initialize database
    const first_time = await dbInit();

    // Initializes the object if there is a need for it.
    if (first_time) await dbInitObject();

    // Apply any pending changes.
    await dbApplyChanges();
  }

  //////////////////////////////////////////////////////////////////////////////

  /**
   * Only ever call this function through the scheduler
   */
  function doApplyChanges(callback: TJobHandlerCallback) {
    dbApplyChanges().then(() => {
      resetDbAccumulation();
      callback({ ok: true });
    }).catch((reason) => {
      throw new Error(reason);
    });
  }

  /**
   * Only ever call this function through the scheduler
   */
  function doAddChanges(callback: TJobHandlerCallback) {
    if (pending_changes.length === 0) {
      // Done
      callback({ ok: true });
      return;
    }

    const changes = pending_changes;
    pending_changes = [];

    const removedLocalAccumulation = resetLocalAccumulation();

    dbAddChanges(changes).then(() => {
      accumulateDbChange(removedLocalAccumulation);
      // Done
      callback({ ok: true });
    }).catch((reason) => {
      // callback??
      console.error(reason);
      throw new Error(reason);
    });
  }

  scheduler.setJobHandler("applychanges", doApplyChanges);
  scheduler.setJobHandler("addchanges", doAddChanges);

  /**
   * Call this function
   */
  function requestApplyChanges() {
    scheduler.scheduleJob("applychanges");
  }

  /**
   * Call this function
   */
  function requestAddChanges() {
    scheduler.scheduleJob("addchanges");
  }

  //////////////////////////////////////////////////////////////////////////////
  // Change accumulation

  function resetLocalAccumulation() {
    const removed = accumulatedLocalChange;
    accumulatedLocalChange = 0;
    return removed;
  }

  function resetDbAccumulation() {
    const removed = accumulatedDbChange;
    accumulatedDbChange = 0;
    return removed;
  }

  function accumulateLocalChange(changeItem: ChangeItem) {
    if (changeItem.type === "begin-transaction" || changeItem.type === "end-transaction") {
      accumulatedLocalChange += changeItem.id.length + changeItem.type.length;
    } else if (changeItem.type === "delete") {
      accumulatedLocalChange += changeItem.jsonPointer.length + changeItem.type.length;
    } else if (changeItem.type === "set") {
      accumulatedLocalChange += changeItem.jsonPointer.length + changeItem.type.length + JSON.stringify(changeItem.value).length;
    } else {
      throw new Error();
    }
  }

  function accumulateDbChange(change: number) {
    assert(change > 0);
    accumulatedDbChange += change;
    if (accumulatedDbChange > ACCUMULATED_CHANGE_LIMIT) {
      requestApplyChanges();
    }
  }

  //////////////////////////////////////////////////////////////////////////////

  /**
   * 
   * @param item 
   */
  function addChangeItem(changeItem: ChangeItem) {
    accumulateLocalChange(changeItem);
    if (transactional) {
      // -----------------------------------------------------------------------
      // TRANSACTIONAL
      //
      dispatchEvent("beforeaddchange", { type: "beforeaddchange", change: changeItem, object: object });

      if (inTransaction) {
        // ---------------------------------------------------------------------
        // IN TRANSACTION
        //

        dispatchEvent("beforechange", { type: "beforechange", change: changeItem, object: object });

        pending_changes.push(changeItem);

        dispatchEvent("change", { type: "change", change: changeItem, object: object });
        dispatchEvent("afterchange", { type: "afterchange", change: changeItem, object: object });
      } else {
        // ---------------------------------------------------------------------
        // NOT IN TRANSACTION
        //

        // An opportunity to create a new transaction.
        dispatchEvent("beforechange", { type: "beforechange", change: changeItem, object: object });

        assert(inTransaction);

        pending_changes.push(changeItem);

        dispatchEvent("change", { type: "change", change: changeItem, object: object });
        dispatchEvent("afterchange", { type: "afterchange", change: changeItem, object: object });
      }

      dispatchEvent("afteraddchange", { type: "afteraddchange", change: changeItem, object: object });
    } else {
      // -----------------------------------------------------------------------
      // NON-TRANSACTIONAL
      //

      // Do not allow transaction if not transactional.
      assert(!(changeItem.type === "begin-transaction" || changeItem.type === "end-transaction"));

      dispatchEvent("beforechange", { type: "beforechange", change: changeItem, object: object });

      pending_changes.push(changeItem);

      dispatchEvent("afterchange", { type: "afterchange", change: changeItem, object: object });
      dispatchEvent("change", { type: "change", change: changeItem, object: object });

      // Add changes after every change (not transactional).
      requestAddChanges();
    }
  }

  //////////////////////////////////////////////////////////////////////////////
  // Proxy stuff

  const observer: TObserver = (changes) => {
    for (const change of changes) {
      const { type } = change;
      if (type === "add" || type === "update") {
        addChangeItem({ type: "set", jsonPointer: change.jsonPointer, value: change.newValue });
      } else if (type === "delete") {
        addChangeItem({ type: "delete", jsonPointer: change.jsonPointer });
      } else {
        throw new Error(`Unknown type: ${type}`);
      }
    }
  };


  //////////////////////////////////////////////////////////////////////////////
  // Transactions

  function getNextTransactionID() {
    return "" + Date.now();
  }

  let inTransaction = false;
  let activeTransactionID: null | string = null;

  function beginTransaction() {
    assert(transactional);
    assert(activeTransactionID === null);

    activeTransactionID = getNextTransactionID();

    pending_changes.push({
      type: "begin-transaction",
      id: activeTransactionID,
    });

    inTransaction = true;
  }

  function endTransaction() {
    assert(transactional);
    assert(activeTransactionID !== null);

    pending_changes.push({
      type: "end-transaction",
      id: activeTransactionID,
    });

    activeTransactionID = null;
    inTransaction = false;

    requestAddChanges();
  }

  //////////////////////////////////////////////////////////////////////////////
  // Stork events and listeners

  function dispatchEvent(type: StorkaEventType, event: StorkaEvent) {
    assert(type === event.type);
    for (const listener of listeners[type]) {
      assert(type === event.type);
      listener(event);
    }
  }

  function addEventListener(type: StorkaEventType, listener: StorkaEventListener) {
    if (!listeners[type].has(listener)) {
      listeners[type].add(listener);

      if (type === "ready") {
        if (is_ready) {
          const event: StorkaEventReady = { type: "ready" };
          listener(event);
        }
      }
    }
  }

  function removeEventListener(type: StorkaEventType, listener: StorkaEventListener) {
    if (listeners[type].has(listener)) {
      listeners[type].delete(listener);
    }
  }

  //////////////////////////////////////////////////////////////////////////////
  // Initialization

  async function init() {
    await dbSetup();

    // Retrieve object
    const retrieved_object = await dbGetObject();

    assert(typeof retrieved_object && retrieved_object !== null);

    object = retrieved_object;


    // Make return object be in a ready state.
    proxy = ObservableSlim.create(object, false, observer);

    return_object.object = object;
    return_object.proxy = proxy;
    return_object.ready = true;
    return_object.beginTransaction = beginTransaction;
    return_object.endTransaction = endTransaction;

    const event: StorkaEventReady = { type: "ready" };

    dispatchEvent("ready", event);
  }

  // Start initialization
  init().catch((reason) => {
    console.log("Error:", reason);
    throw new Error(reason);
  });

  //////////////////////////////////////////////////////////////////////////////
  // Return object

  const return_object: StorkaReturnObject = {
    object: null,
    proxy: null,
    addEventListener,
    removeEventListener,
    ready: false,

    beginTransaction: () => { },
    endTransaction: () => { },
  };

  //////////////////////////////////////////////////////////////////////////////
  // Exports

  return return_object;
}
