import React from "react";

import { deepMixIn } from "../../utils-ui";
import CSS from 'csstype';

import "./index.css";

type Props = {
  [key: string]: any
}

const MobileLayout: React.FC<Props> = (props) =>  {
  const children = props.hasOwnProperty("children")
    ? Array.isArray(props.children)
      ? props.children
      : [props.children]
    : [];

  const upper_self_props: any = {};
  Object.keys(props).forEach((propName) => {
    if (propName !== "children") {
      upper_self_props[propName] = props[propName];
    }
  });

  const self_props = {
    style: {
      display: "flex",
      height: "100%",
      width: "100%",
      flexDirection: "column",
    } as CSS.Properties,
  };

  deepMixIn(upper_self_props, self_props);

  return (<div {...self_props}>{children}</div>);
}

export default MobileLayout;
