export function createEvent(state: GTMState) {
  throw new Error("Not implemented!");
}

export function getEvent(state: GTMState, id: string) {
  assert(hasOwnProperty(state.entities, id));
  const entity = state.entities[id];
  assert(entity.type === "event");
  return entity;
}

