import React /*, { useState } */ from "react";

import HCFLayout from "../../layout/hcf";
import { Navi } from '../../navi';

import { GTMApp } from "../../gtm-app/gtm";

import "./index.css";


interface Props {
  gtm: GTMApp;
  navi: Navi;
}


/**
 * URL: `gtm:/creation-selection`.
 * 
 * @param props 
 */
const PageCreationSelection: React.FC<Props> = (props) => {
  const { navi, gtm } = props;

  // const [text, setText] = useState("http://");


  const header = (
    <div key={1} className="topBar">
      <div>
        <button onClick={() => { navi.back(); }}>Go back</button>
      </div>
    </div>
  );

  /*

        <button
        onClick={() => {
          navi.setURL(`gtm:/view`, 1);
        }}
      >
        Create view
      </button>

  */

  const content = (
    <div key={2} className="page-creation-selection">

      <button
        onClick={() => {
          const task = gtm.task.createTask();
          navi.setURL(`gtm:/task/${task.id}`, 1);
        }}
      >
        Create task
      </button>
    </div>
  );

  /*
  const footer = (
    <div key={3} className="bottomBar">
      ...
    </div>
  );
  */

  return (
    <HCFLayout>
      {header}
      {content}
      {null}
    </HCFLayout>
  );
}


export default PageCreationSelection;
