import React from "react";

import HCFLayout from "../../layout/hcf";

import GanttView from "../../component/gantt-view";

import { Navi } from '../../navi';
import { GTMApp } from "../../gtm-app/gtm";

import "./index.css";



interface IProps {
  navi: Navi
  gtm: GTMApp,

}


/**
 * URL: `gtm:/gantt`
 * 
 * @param props 
 */
const PageGantt: React.FC<IProps> = (props) => {
  const { navi } = props;

  const header = (
    <div key={1} className="topBar"
      style={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between"
      }}
    >
      <div>
        <button
          onClick={() => {
            navi.back();
          }}
        >
          Go back
        </button>
      </div>
    </div>
  );

  const content = (
    <div key={2} className="page-gantt__content">
      <GanttView/>
    </div>
  );

  const footer = (
    <div key={3} className="bottomBar page-home__footer">
      Footer
    </div>
  );


  return (
    <HCFLayout>
      {header}
      {content}
      {footer}
    </HCFLayout>
  );
}


export default PageGantt;
