import React from "react";


import HCFLayout from "../../layout/hcf";

import TaskListItem from "../../component/task-list-item";

import { Navi } from '../../navi';
import { GTMApp } from "../../gtm-app/gtm";

import "./index.css";


type Props = {
  gtm: GTMApp,
  navi: Navi
}

/**
 * URL: `gtm:/tasklist`
 * 
 * 
 * gtm:/tasklist?id=...
 * 
 * @param props 
 */
const PageTaskList: React.FC<Props> = (props) => {
  const { gtm, navi } = props;

  const id = navi.urlObj.queryObject.id;


  let title = "Root tasks";


  let task_ids: string[] = [];

  let super_task_ids: string[] = [];
  let sub_task_ids: string[] = [];

  let has_task_view = false;


  if (typeof id === "string") {

    if (gtm.task.hasTask(id)) {
      has_task_view = true;
      const task = gtm.task.getTask(id);

      title = task.id + ": " + task.description;
      if (title.length > 20) {
        title = title.substring(0, 17) + "...";
      }

      super_task_ids = [...task.dependents];
      sub_task_ids = [...task.dependencies];

    } else {
      navi.back();
    }
  }

  task_ids = gtm.task.listTaskIDs([
    ["filter", ["roots"]],
    ["sort", ["status", "open", "asc"], ["date_modified", "desc"]],
  ]);

  const header = (
    <div key={1} className="topBar"
      style={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between"
      }}
    >
      <div>
        <button
          onClick={() => {
            navi.back();
          }}
        >
          Go back
        </button>
        {
          has_task_view
            ?
            (
              <button onClick={() => {
                navi.setURL(`gtm:/task/${id}?mode=edit`);
              }}>
                Edit
            </button>
            )
            :
            null
        }
        {title}
      </div>
      <div>
        <button
          onClick={() => {
            navi.setURL("gtm:/home");
          }}
        >
          Go home
      </button>
      </div>
    </div>
  );

  let content;
  if (has_task_view) {

    if (super_task_ids.length === 0) {
      content = (
        <div key={2}>
          Roots
          <ul>
            {task_ids.map((task_id, index) => {
              return (
                <TaskListItem
                  key={index}
                  task_id={task_id}
                  gtm={gtm}
                  navi={navi}
                ></TaskListItem>
              );
            })}
          </ul>
          Task
        <ul>
            <TaskListItem
              task_id={id}
              gtm={gtm}
              navi={navi}
            ></TaskListItem>
          </ul>
          Sub-tasks
        <ul>
            {sub_task_ids.map((task_id, index) => {
              return (
                <TaskListItem
                  key={index}
                  task_id={task_id}
                  gtm={gtm}
                  navi={navi}
                ></TaskListItem>
              );
            })}
          </ul>
        </div>
      );
    } else {
      content = (
        <div key={2}>
          Super-tasks
          <ul>
            {super_task_ids.map((task_id, index) => {
              return (
                <TaskListItem
                  key={index}
                  task_id={task_id}
                  gtm={gtm}
                  navi={navi}
                ></TaskListItem>
              );
            })}
          </ul>

          Task
        <ul>
            <TaskListItem
              task_id={id}
              gtm={gtm}
              navi={navi}
            ></TaskListItem>
          </ul>
          Sub-tasks
        <ul>
            {sub_task_ids.map((task_id, index) => {
              return (
                <TaskListItem
                  key={index}
                  task_id={task_id}
                  gtm={gtm}
                  navi={navi}
                ></TaskListItem>
              );
            })}
          </ul>
        </div>
      );
    }
  } else {
    content = (
      <div key={2}>
        <ul>
          {task_ids.map((task_id, index) => {
            return (
              <TaskListItem
                key={index}
                task_id={task_id}
                gtm={gtm}
                navi={navi}
              ></TaskListItem>
            );
          })}
        </ul>
      </div>
    );
  }



  const footer = React.createElement(
    "div",
    {
      key: "3",
      className: "bottomBar",
      style: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-end",
      },
    },
    <button
      onClick={() => {
        if (has_task_view) {
          navi.setURL(
            `gtm:/edit-depend?${[
              ["id", id],
              ["type", "dependency"],
              ["mode", "add"],
            ]
              .map((x) => x.map((y) => escape(y)).join("="))
              .join("&")}`
          );
        } else {
          const task = gtm.task.createTask();
          navi.setURL(`gtm:/task/${task.id}`);
        }
      }}
    >
      &nbsp;+&nbsp;
    </button>
  );

  return (
    <HCFLayout>
      {header}
      {content}
      {footer}
    </HCFLayout>
  );
}


export default PageTaskList;
