import React from "react";

import "./index.css";

type Props = {
  active: boolean,
  onChange: (active: boolean) => void
}

const PlayButton: React.FC<Props> = (props) => {
  const { active, onChange } = props;
  return (
    <button
      onClick={() => {
        onChange(!active);
      }}
    >{ active ? "" : ""}</button>
  );
}

export default PlayButton;
