import React, { useState } from "react";

import HCFLayout from "../../layout/hcf";
import { Navi } from '../../navi';

import { GTMApp } from "../../gtm-app/gtm";

import "./index.css";


function getListFn(gtm: GTMApp, type: string, mode: string) {
  if (type === "dependency") {
    if (mode === "add") {
      // Add dependency
      return (id: string) => {
        return gtm.task.listTaskIDs([
          ["filter", ["possible-new-dependencies-of", id]],
          ["sort", ["date_modified", "desc"]],
        ]);
      };
    } else if (mode === "remove") {
      // Remove dependency
      return (id: string) => {
        return gtm.task.listTaskIDs([
          ["filter", ["dependencies-of", id]]
        ]);
      };
    } else {
      throw new Error();
    }
  } else if (type === "dependent") {
    if (mode === "add") {
      // Add possible dependent
      return (id: string) => {
        return gtm.task.listTaskIDs([
          ["filter", ["possible-new-dependents-of", id]],
          ["sort", ["date_modified", "desc"]],
        ])
      };
    } else if (mode === "remove") {
      // Remove depedent
      return (id: string) => {
        return gtm.task.listTaskIDs([
          ["filter", ["dependents-of", id]]
        ]);
      };
    } else {
      throw new Error("");
    }
  } else {
    throw new Error("");
  }
}

function getActionFn(gtm: GTMApp, type: string, mode: string) {
  if (type === "dependency") {
    if (mode === "add") {
      // Add dependency
      return (id: string, did: string) => {
        gtm.task.addDependency(id, did);
      };
    } else if (mode === "remove") {
      // Remove dependency
      return (id: string, did: string) => {
        gtm.task.removeDependency(id, did);
      };
    } else {
      throw new Error();
    }
  } else if (type === "dependent") {
    if (mode === "add") {
      // Add dependent
      return (id: string, did: string) => {
        gtm.task.addDependent(id, did);
      };
    } else if (mode === "remove") {
      // Remove dependent
      return (id: string, did: string) => {
        gtm.task.removeDependent(id, did);
      };
    } else {
      throw new Error("");
    }
  } else {
    throw new Error("");
  }
}


interface Props {
  gtm: GTMApp;
  navi: Navi;
}


/**
 * URL: `gtm:/edit-depend`
 * 
 * @param props 
 */
const PageEditDepend: React.FC<Props> = (props) => {
  const { navi, gtm } = props;

  if (navi.urlObj === null) {
    // type guard
    throw new Error();
  }

  const { id, type, mode } = navi.urlObj.queryObject;

  const list_fn = getListFn(gtm, type, mode);
  const action_fn = getActionFn(gtm, type, mode);

  

  const [dids, setDids] = useState<string[]>(list_fn(id));

  function handleChange(did: string) {
    action_fn(id, did);
    setDids(list_fn(id));
  }

  // ---------------------------------------------------------------------------
  // Header
  const header = React.createElement(
    "div",
    {
      key: "1",
      className: "topBar",
    },
    React.createElement(
      "div",
      {},
      React.createElement(
        "button",
        {
          onClick: () => {
            navi.back();
          },
        },
        "Go back"
      ),
      // `ID: ${id} | Type: ${type} | Mode: ${mode}`
      mode === "add"
        ? `Add ${
        type === "dependency"
          ? "subtask"
          : "supertask"
        } to ${id}`
        : `Remove ${
        type === "dependency"
          ? "subtask"
          : "supertask"
        } from ${id}`
    )
  );

  // ---------------------------------------------------------------------------
  // Content
  const content = React.createElement(
    "div",
    {
      key: "2",
    },

    ...dids.map((did) => {
      const task = gtm.task.getTask(did);

      const n = 20;

      const short_description =
        task.description.length <= n
          ? task.description
          : task.description.substring(0, n - 3) + "...";
      const name = `${did}: ${short_description}`;

      return React.createElement(
        "button",
        {
          key: did,
          onClick: (e) => {
            handleChange(did);
          },
        },
        name
      );
    })
  );

  // ---------------------------------------------------------------------------
  // Footer


  const footer = (
    <div key={3} className="bottomBar" style={{ display: "flex", flexDirection: "row-reverse" }}>
      {
        mode === "add" ? 
        <button onClick={() => {
          const task = gtm.task.createTask();
          if (type === "dependency") {
            gtm.task.addDependency(id, task.id);
          } else if (type === "dependent") {
            gtm.task.addDependent(id, task.id);
          } else {
            throw new Error();
          }
          navi.setURL(`gtm:/task/${task.id}`, 1);
        }}>+</button> :
        null
      }
    </div>
  );

  return (
    <HCFLayout>
      {header}
      {content}
      {footer}
    </HCFLayout>
  );

}


export default PageEditDepend;
