////////////////////////////////////////////////////////////////////////////////
// GTM states

interface State_V_0_16_0 {
  version: "0.16.0",
  storage: {
    graph: {
      nodes: {
        [id: string]: {
          id: string,
          type: string,
          description: string,
          dependencies: string[],
          dependents: string[],
          date_modified: string,
          date_created: string,
          date_user_modified: string,
          date_done: null | string,
          date_removed: null | string,
          date_abandoned: null | string,
          date_due: null | string,
          time_estimated: null | number,
          tags: string[],
          priority: number,
          status: "open" | "done" | "removed" | "abandoned",
          worktime: string[][], // NOTE: not correct
          comments: string[]
        }
      }
    },
    _next_id: number,
    _work_task_id: null | string,
    _work_task_active: boolean
  }
};

interface State_V_20 {
  version: 20;
  entities: {
    [id: string]: GTMEntity_v20;
  };

  next_id: number;

  // If the activity stack is empty, then it's OK to submit changes, otherwise
  // it is in an intermediate state.
  activity_stack: number[];

  misc: {
    active_task_ids: string[];
  };
};

interface GTMState_v20 {
  version: 20,
  entities: {
    [id: string]: GTMEntity_v20,
  },
  next_id: number,
  // If the activity stack is empty, then it's OK to submit changes, otherwise
  // it is in an intermediate state.
  activity_stack: number[],
  misc: {
    active_task_ids: string[],
  },
};

export interface GTMState_v21 {
  version: 21,
  entities: {
    [id: string]: GTMEntity_v20,
  },
  next_id: number,
  // Transaction is set with `Date.now()` before a GTM function is called and
  // afterwards it is compared.
  transaction: string,
  misc: {
    active_task_ids: string[],
  },
};

// CHANGES
// 
// * ID's are randomly generated, and ought to be unique.
// * Each entity should generate a random ID.
// * No more `state.next_id`.
// * To begin with, each ID is random 128-bit number encoded in Base64 with the
//   62nd key as `+` and the 63rd key as `/` and `=` as padding.  (I think 
//   `-` and `_` are better choices).  The 62nd is `-` and 63rd is `_` and the
//   padding is `=` and is optional.
// * "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/"
interface GTMState_v22 {
  version: 22,
  entities: {
    [id: string]: GTMEntity_v20,
  },
  // Miscellaneous properties associated with `entities`.
  misc: {
    active_task_ids: string[],
  },
  // Transaction is set with `Date.now()` before a GTM function is called and
  // afterwards it is compared.
  transaction: string,
  // A 1024-bit random string.  256-512 bits are reserved for generating 
  // private and public keys, and another 256-512 bits to generate symmetric
  // keys.
  token: string,
}


