// Entities

/**
 * @param state 
 * @param id 
 * @throws If the entity with ID `task_id` is not of type `task` then an exception is raised.
 */
export function hasEntity(state: GTMState, id: string) {
  if (!hasOwnProperty(state.entities, id)) return false;
  return true;
}

export function verifyEntity(state: GTMState, id: string) {
  // TODO: if entity with ID does not exist, throw error!
  throw new Error("Not implemented!");
}

/**
 * 
 * @param state 
 * @param id 
 * @throws throws error if task doesn't exist with given ID
 */
export function getEntity(state: GTMState, id: string) {
  assert(hasOwnProperty(state.entities, id));
  const entity = state.entities[id];
  return entity;
}

export function addEntity(state: GTMState, entity: GTMEntity_v20) {
  assert(typeof entity === "object" && entity !== null);
  assert(hasOwnProperty(entity, "id"));
  assert(typeof entity.id === "string");
  assert(entity.id !== "");
  assert(!hasOwnProperty(state, entity.id));

  state.entities[entity.id] = entity;
}

export function removeEntity(state: GTMState, id: string) {
  assert(hasOwnProperty(state.entities, id));
  delete state.entities[id];
}

////////////////////////////////////////////////////////////////////////////////
// Export and import

/**
 * 
 * If `json` is set to `true` then the export text will just be JSON.
 * 
 * @param state 
 * @param json 
 */
export function doExport(state: GTMState, json=false) {
  // TODO: we want to make sure that we don't lose any information exporting
  // data.
  if (json) {
    return JSON.stringify(state);
  } else {
    return LZString.compressToBase64(JSON.stringify(state));  
  }
}

/**
 * 
 * @param state 
 * @param import_value 
 * @param json 
 */
export function doImport(state: GTMState, import_value: string, json=false) {
  let new_state: any;
  let status = -1;

  // Parse JSON
  if (status === -1) {
    try {
      new_state = JSON.parse(import_value);
      status = 0;
    } catch (e) { }
  }

  // Decompress and then parse JSON
  if (status === -1) {
    try {
      const decompressed_import_value = LZString.decompressFromBase64(import_value);
      if (decompressed_import_value !== null) {
        new_state = JSON.parse(decompressed_import_value);
        status = 0;
      }
    } catch (e) { }
  }

  // Cannot import
  if (status === -1) {
    throw new Error(`Cannot import value: ${import_value}`);
  }

  assert(typeof new_state === "object" && new_state !== null);
  assert(hasOwnProperty(new_state, "version"));

  if (typeof new_state.version === "string") {
    new_state = migrate(new_state);
  } else if (typeof new_state.version === "number") {
    if (new_state.version !== CURRENT_VERSION) {
      new_state = migrate(new_state);
    }
  }

  assert(typeof new_state === "object" && new_state !== null);
  assert(hasOwnProperty(new_state, "version"));
  assert(new_state.version === CURRENT_VERSION);

  // Delete old keys
  Object.keys(state).forEach((key) => {
    if (key !== "transaction") {
      delete (state as any)[key];
    }
  });

  // Add new keys
  Object.keys(new_state).forEach((key) => {
    if (key !== "transaction") {
      (state as any)[key] = new_state[key];
    }
  });
}

////////////////////////////////////////////////////////////////////////////////
// Common things

/**
 * This function is about to be deprecated!
 * 
 * @param state 
 */
export function getNextID(state: GTMState) {
  const nextID = state.next_id;
  state.next_id += 1;
  return "" + nextID;
}



export function getState(state: any): GTMState {
  assert(verifyState(state, true));
  return state;
}

////////////////////////////////////////////////////////////////////////////////
// Transaction

export function beginTransaction(state: GTMState_v21) {
  if (state.hasOwnProperty("transaction")) {
    assert(state.transaction === "" || (state.transaction.length > 0 && state.transaction[0] === "("));
  }


  const value = ")" + Date.now();

  state.transaction = value;

  return value;
}

export function endTransaction(state: GTMState_v21, expectedValue: string) {
  assert(state.hasOwnProperty("transaction"));
  assert(state.transaction.length > 0);
  assert(state.transaction[0] === ")");
  assert(state.transaction === expectedValue);

  const value = "(" + expectedValue;

  state.transaction = value;

  return value;
}

