const getNewState = getNewState_v21;

////////////////////////////////////////////////////////////////////////////////
// Migration

function migrate_from_v_0_16_0_to_v_20(oldState: State_V_0_16_0) {

  function getNextID(state: State_V_20) {
    const nextID = state.next_id
    state.next_id += 1;
    return "" + nextID;
  }

  const new_state = getNewState_v20();

  // Update next ID
  new_state.next_id = oldState.storage._next_id;
  // Update tasks  

  const old_task_ids = Object.keys(oldState.storage.graph.nodes);

  const comments_changes = [];

  for (let i = 0; i < old_task_ids.length; i++) {
    const old_task_id = old_task_ids[i];
    const old_task = oldState.storage.graph.nodes[old_task_id];

    let new_status: string = old_task.status;
    if (new_status === "abandoned") {
      new_status = "archived";
    }

    comments_changes.push({
      id: old_task_id,
      comments: old_task.comments,
    })

    const new_task: GTMEntityTask_v20 = {
      type: "task",
      id: old_task_id,

      title: "",
      description: old_task.description,

      date_created: old_task.date_created,
      date_modified: old_task.date_modified,

      date_due: old_task.date_due,

      date_opened: null,
      date_done: old_task.date_done,
      date_archived: old_task.date_abandoned,
      date_removed: old_task.date_removed,


      dependencies: old_task.dependencies,
      dependents: old_task.dependents,

      // Estimated time in milliseconds
      estimated_time: old_task.time_estimated,

      priority: old_task.priority,

      // @ts-ignore
      status: new_status,

      // @ts-ignore
      worktime: old_task.worktime,

      tags: old_task.tags,

      // Refers to `note` elements.
      comments: [],
    };

    // Need to add date_opened
    if (old_task.status === "open") {
      new_task.date_opened = new_task.date_created;
    }

    new_state.entities[new_task.id] = new_task;
  }

  // Add comments
  for (const comments_change of comments_changes) {
    // TODO: FIX!
    const task_id = comments_change.id;
    const entity = new_state.entities[task_id];
    assert(entity.type === "task");
    const task = entity;
    const task_comments = comments_change.comments;


    for (const task_comment of task_comments) {
      const id = getNextID(new_state);

      const note: GTMEntityNote_v1 = {
        type: "note",
        id,

        title: "",
        text: task_comment,

        date_created: task.date_created,
        date_modified: task.date_created,
      };

      new_state.entities[id] = note;
    }
  }

  return new_state;
}

function migrate_from_v_20_to_v_21(oldState: GTMState_v20) {
  assert(oldState.activity_stack.length === 0);

  const newState = getNewState_v21();

  newState.entities = oldState.entities;
  newState.next_id = oldState.next_id;
  newState.misc.active_task_ids = oldState.misc.active_task_ids;

  return newState;
}

function migrate_from_v_21_to_v_22(oldState: GTMState_v21): GTMState_v22 {
  if (Math.random() > -1) throw new Error("Not implemented!");

  const state = getNewState_v22();

  // Copy from old state
  state.entities = oldState.entities
  state.misc.active_task_ids = oldState.misc.active_task_ids;
  state.transaction = oldState.transaction;

  const mapOldIDToNewID: { [oldID: string]: string } = {};

  // Every single ID must be renamed
  const generateID = () => {
    if (Math.random() > -1) throw new Error("Not implemented!");
    return "";
  };

  const addToMap = (oldID: string) => {
    if (!mapOldIDToNewID.hasOwnProperty(oldID)) {
      mapOldIDToNewID[oldID] = generateID();
    }
  };

  const getNewID = (oldID: string) => {
    assert(mapOldIDToNewID.hasOwnProperty(oldID));
    return mapOldIDToNewID[oldID];
  };

  // Walk over everything, gather IDs
  Object.keys(state.entities).forEach((entity_id) => {
    const entity = state.entities[entity_id];
    addToMap(entity_id);
    assert(entity.id === entity_id);
    switch (entity.type) {
      case "calendar":
        break;
      case "dependency":
        assert(state.entities.hasOwnProperty(entity.source_id));
        assert(state.entities.hasOwnProperty(entity.target_id));
        addToMap(entity.source_id);
        addToMap(entity.target_id);
        break;
      case "event":
        assert(state.entities.hasOwnProperty(entity.calendar_id));
        addToMap(entity.calendar_id);
        break;
      case "link":
        assert(state.entities.hasOwnProperty(entity.source_id));
        assert(state.entities.hasOwnProperty(entity.target_id));
        addToMap(entity.source_id);
        addToMap(entity.target_id);
        break;
      case "note":
        break;
      case "poset":
        break;
      case "reminder":
        break;
      case "tag":
        break;
      case "task":
        Object.keys(entity.dependencies).forEach((dep_id) => {
          assert(state.entities.hasOwnProperty(dep_id));
          addToMap(dep_id);
        });
        Object.keys(entity.dependents).forEach((dep_id) => {
          assert(state.entities.hasOwnProperty(dep_id));
          addToMap(dep_id);
        });
        Object.keys(entity.comments).forEach((com_id) => {
          assert(state.entities.hasOwnProperty(com_id));
          addToMap(com_id);
        });
        break;
      default:
        throw new Error(`Unknown error!`);
    }
  });

  const oldEntities = state.entities;

  state.entities = {};

  // Remap ALL IDs
  Object.keys(oldEntities).forEach((oldEntityID) => {
    const entity = oldEntities[oldEntityID];
    const newEntityID = getNewID(oldEntityID);
    state.entities[newEntityID] = entity;
    entity.id = newEntityID;
    switch (entity.type) {
      case "calendar":
        break;
      case "dependency":
        entity.source_id = getNewID(entity.source_id);
        entity.target_id = getNewID(entity.target_id);
        break;
      case "event":
        entity.calendar_id = getNewID(entity.calendar_id);
        break;
      case "link":
        entity.source_id = getNewID(entity.source_id);
        entity.target_id = getNewID(entity.target_id);
        break;
      case "note":
        break;
      case "poset":
        break;
      case "reminder":
        break;
      case "tag":
        break;
      case "task":
        Object.keys(entity.dependencies).forEach((dep_id, idx) => {
          entity.dependencies[idx] = getNewID(dep_id);
        });
        Object.keys(entity.dependents).forEach((dep_id, idx) => {
          entity.dependents[idx] = getNewID(dep_id);
        });
        Object.keys(entity.comments).forEach((com_id, idx) => {
          entity.dependents[idx] = getNewID(com_id);
        });
        break;
      default:
        throw new Error(`Unknown error!`);
    }
  });

  state.misc.active_task_ids.forEach((id, idx) => {
    state.misc.active_task_ids[idx] = getNewID(id);
  });


  return state;
}

/**
 * Takes an old state and returns a new state.
 * 
 * @param state 
 */
function doMigrate(state: any) {
  if (state.version === "0.16.0") {
    return {
      ok: true,
      state: migrate_from_v_0_16_0_to_v_20(state)
    };
  } else if (state.version === 20) {
    return {
      ok: true,
      state: migrate_from_v_20_to_v_21(state)
    };
  } else {
    console.error(`Unknown state version: ${state.version}`);
  }

  return {
    ok: false
  };
}

export function migrate(inputState: any) {
  assert(typeof inputState === "object" && inputState !== null);
  assert(hasOwnProperty(inputState, "version"));

  // Remove proxies and other garbage.
  let migratedState = JSON.parse(JSON.stringify(inputState));

  if (inputState.version === CURRENT_VERSION) {
    if (DEBUG) console.info(`migrate(): no need to migrate`);
    return migratedState;
  }

  let ok = true;

  while (ok && migratedState.version !== CURRENT_VERSION) {
    if (DEBUG) console.info(`migrate(): migrating from version: ${migratedState.version}`);
    const migrationResult = doMigrate(migratedState);

    ok = migrationResult.ok;

    if (DEBUG) console.info(`migrate(): migrating from version is ok?: ${ok}`);

    if (migrationResult.ok) {
      migratedState = migrationResult.state;
    }
  }

  if (ok) {
    return migratedState;
  } else {
    throw new Error(`Unable to migrate!`);
  }
}

