import React from "react";

import HCFLayout from "../../layout/hcf";
import { Navi } from '../../navi';


import "./index.css";

interface IProps {
  navi: Navi
}


/**
 * URL: `gtm:/about`
 * 
 * @param props 
 */
const PageAbout: React.FC<IProps> = (props) => {
  const { navi } = props;

  const header = (
    <div key={1} className="topBar"
      style={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between"
      }}
    >
      <div>
        <button
          onClick={() => {
            navi.back();
          }}
        >
          Go back
        </button>
      </div>
      <div>
        <button
          onClick={() => {
            navi.setURL("gtm:/home");
          }}
        >
          Go home
      </button>
      </div>
    </div>
  );

  const content = (
    <div>
      Last updated: 2020-02-27 15:51
    </div>
  );

  const footer = (
    <div key={3} className="bottomBar" style={{ display: "flex", flexDirection: "row", justifyContent: "flex-end" }}>
    </div>
  );

  return (
    <HCFLayout>
      {header}
      {content}
      {footer}
    </HCFLayout>
  );
}


export default PageAbout;
