import { createStorka, StorkaEvent } from "../gtm-storage/storka";
import { assert } from "../utils-ui";
import { lib } from "./gtm-lib";


export type GTMApp = ReturnType<typeof createGTMApp>;

/**
 * The input state `inputState` might be a regular object or a proxy of an
 * object.
 * 
 * @param inputState 
 */
function createGTMApp(inputState: any) {
  const _this = null;

  assert(typeof inputState === "object" && inputState !== null);

  // Proxy might be empty
  if (!lib.common.verifyState(inputState, true)) lib.common.setup(inputState);

  const state = inputState;

  /**
   * Wraps GTM function `fn` such that it is not transactional, i.e. does
   * nothing but makes it explicit.
   * 
   * @param fn 
   */
  const wrapNonTransaction = <A extends Array<any>, R>(fn: (...args: A) => R) => {
    return fn;
  }; 

  /**
   * Wraps GTM function `fn` such that it becomes transactional, i.e. changes
   * we keep track of when we start the transaction and end it when it comes
   * to changing state `state`.
   * 
   * This is mainly used by Storka.
   * 
   * @param fn 
   */
  const wrapTransaction = <T extends Array<any>, U>(fn: (...args: T) => U) => {
    return (...args: T): U => {
      const transactionValue = lib.common.beginTransaction(state);
      const result = fn(...args);
      lib.common.endTransaction(state, transactionValue);
      return result;
    };
  };

  // TODO: find a better way to do this
  const app = {
    calendar: {
      // Non-transactional
      getCalendar: wrapNonTransaction(lib.calendar.getCalendar.bind(_this, state)),

      // Transactional
    },
    common: {
      // Non-transactional
      doExport: wrapNonTransaction(lib.common.doExport.bind(_this, state)),

      // Transactional
      doImport: wrapTransaction(lib.common.doImport.bind(_this, state)),
    },
    dependency: {},
    event: {},
    link: {},
    note: {
      // Non-transactional
      getNote: wrapNonTransaction(lib.note.getNote.bind(_this, state)),

      // Transactional
      changeText: wrapTransaction(lib.note.changeText.bind(_this, state)),
      createNote: wrapTransaction(lib.note.createNote.bind(_this, state)),
    },
    poset: {},
    reminder: {},
    task: {
      // Non-transactional
      getActiveTaskIDs: wrapNonTransaction(lib.task.getActiveTaskIDs.bind(_this, state)),
      getActiveTasks: wrapNonTransaction(lib.task.getActiveTasks.bind(_this, state)),
      getDescendantIDs: wrapNonTransaction(lib.task.getDescendantIDs.bind(_this, state)),
      getPossibleDependencyIDs: wrapNonTransaction(lib.task.getPossibleDependencyIDs.bind(_this, state)),
      getPossibleDependentIDs: wrapNonTransaction(lib.task.getPossibleDependentIDs.bind(_this, state)),
      getSelfEstimatedTime: wrapNonTransaction(lib.task.getSelfEstimatedTime.bind(_this, state)),
      getTask: wrapNonTransaction(lib.task.getTask.bind(_this, state)),
      getTaskDependencyIDs: wrapNonTransaction(lib.task.getTaskDependencyIDs.bind(_this, state)),
      getTaskDependentIDs: wrapNonTransaction(lib.task.getTaskDependentIDs.bind(_this, state)),
      getTaskSelfWorktime: wrapNonTransaction(lib.task.getTaskSelfWorktime.bind(_this, state)),
      getTaskTotalWorktime: wrapNonTransaction(lib.task.getTaskTotalWorktime.bind(_this, state)),
      getTotalDependencyGraph: wrapNonTransaction(lib.task.getTotalDependencyGraph.bind(_this, state)),
      getTotalEstimatedTime: wrapNonTransaction(lib.task.getTotalEstimatedTime.bind(_this, state)),
      isTask: wrapNonTransaction(lib.task.isTask.bind(_this, state)),
      isTaskActive: wrapNonTransaction(lib.task.isTaskActive.bind(_this, state)),
      listTaskIDs: wrapNonTransaction(lib.task.listTaskIDs.bind(_this, state)),
      listTasks: wrapNonTransaction(lib.task.listTasks.bind(_this, state)),
      getComment: wrapNonTransaction(lib.task.getComment.bind(_this, state)),
      hasDueDate: wrapNonTransaction(lib.task.hasDueDate.bind(_this, state)),
      getDueDate: wrapNonTransaction(lib.task.getDueDate.bind(_this, state)),
      isDone: wrapNonTransaction(lib.task.isDone.bind(_this, state)),
      getDateDone: wrapNonTransaction(lib.task.getDateDone.bind(_this, state)),
      isRemoved: wrapNonTransaction(lib.task.isRemoved.bind(_this, state)),
      getDateRemoved: wrapNonTransaction(lib.task.getDateRemoved.bind(_this, state)),
      isArchived: wrapNonTransaction(lib.task.isArchived.bind(_this, state)),
      getDateArchived: wrapNonTransaction(lib.task.getDateArchived.bind(_this, state)),
      hasEstimatedTime: wrapNonTransaction(lib.task.hasEstimatedTime.bind(_this, state)),
      getEstimatedTime: wrapNonTransaction(lib.task.getEstimatedTime.bind(_this, state)),
      isOpen: wrapNonTransaction(lib.task.isOpen.bind(_this, state)),

      hasTask: wrapNonTransaction(lib.task.hasTask.bind(_this, state)),

      // Transactional
      addComment: wrapTransaction(lib.task.addComment.bind(_this, state)),
      addDependency: wrapTransaction(lib.task.addDependency.bind(_this, state)),
      addDependent: wrapTransaction(lib.task.addDependent.bind(_this, state)),
      addTag: wrapTransaction(lib.task.addTag.bind(_this, state)),
      archiveTask: wrapTransaction(lib.task.archiveTask.bind(_this, state)),
      changeComment: wrapTransaction(lib.task.changeComment.bind(_this, state)),
      changeDescription: wrapTransaction(lib.task.changeDescription.bind(_this, state)),
      changeDueDate: wrapTransaction(lib.task.changeDueDate.bind(_this, state)),
      changeEstimatedTime: wrapTransaction(lib.task.changeEstimatedTime.bind(_this, state)),
      changePriority: wrapTransaction(lib.task.changePriority.bind(_this, state)),
      createTask: wrapTransaction(lib.task.createTask.bind(_this, state)),
      deleteTask: wrapTransaction(lib.task.deleteTask.bind(_this, state)),
      finishTask: wrapTransaction(lib.task.finishTask.bind(_this, state)),
      openTask: wrapTransaction(lib.task.openTask.bind(_this, state)),
      removeComment: wrapTransaction(lib.task.removeComment.bind(_this, state)),
      removeDependency: wrapTransaction(lib.task.removeDependency.bind(_this, state)),
      removeDependent: wrapTransaction(lib.task.removeDependent.bind(_this, state)),
      removeDueDate: wrapTransaction(lib.task.removeDueDate.bind(_this, state)),
      removeEstimatedTime: wrapTransaction(lib.task.removeEstimatedTime.bind(_this, state)),
      removeTag: wrapTransaction(lib.task.removeTag.bind(_this, state)),
      removeTask: wrapTransaction(lib.task.removeTask.bind(_this, state)),
      rewindTask: wrapTransaction(lib.task.rewindTask.bind(_this, state)),
      startTask: wrapTransaction(lib.task.startTask.bind(_this, state)),
      stopAllTasks: wrapTransaction(lib.task.stopAllTasks.bind(_this, state)),
      stopTask: wrapTransaction(lib.task.stopTask.bind(_this, state)),
      reorderDependents: wrapTransaction(lib.task.reorderDependents.bind(_this, state)),
      reorderDependencies: wrapTransaction(lib.task.reorderDependencies.bind(_this, state)), 
    }
  };

  return app;
}


/**
 * Creates a GTM "native" application (horrible name) using IndexedDB database
 * `database_name` as its backend.
 * 
 * @param database_name 
 */
export async function getGTMNativeApp(database_name: string) {
  // database_name
  const storka = createStorka(database_name, { transactional: true });

  // Wait for `storka` to become ready.
  await (new Promise((resolve, reject) => {
    storka.addEventListener("ready", (evt) => {
      resolve();
    });
  }));

  // Add event listeners to listen for start of transactions.
  storka.addEventListener("beforeaddchange", (evt: StorkaEvent) => {
    assert(evt.type === "beforeaddchange"); // TODO: fix in the future
    const { change } = evt;
    if (change.type === "set") {
      if (change.jsonPointer === "/transaction" && change.value[0] === ")") {
        // Begin a transaction
        storka.beginTransaction();
      }
    }
  });

  // Add event listeners to listen for end of transactions.
  storka.addEventListener("afteraddchange", (evt: StorkaEvent) => {
    assert(evt.type === "afteraddchange"); // TODO: fix in the future
    const { change } = evt;

    if (change.type === "set") {
      if (change.jsonPointer === "/transaction" && change.value[0] === "(") {
        // End a transaction
        storka.endTransaction();
      }
    }
  });

  const object = storka.object;
  const proxy = storka.proxy;

  assert(typeof proxy === "object" && proxy !== null);
  assert(typeof object === "object" && object !== null);

  // TODO: remove when not debugging
  if (true) {
    (window as any).state_proxy = proxy;
    (window as any).state = object;  
  }

  lib.common.verifyState(proxy);

  const app = createGTMApp(proxy);

  return app;
}
