import React from "react";

import HCFLayout from "../../layout/hcf";
import { Navi } from '../../navi';
import { Routes } from '../../App';

import { GTMApp } from "../../gtm-app/gtm";
import { assert } from "../../utils-ui";

import "./index.css";

type Props = {
  navi: Navi,
  routes: Routes,
  gtm: GTMApp,
}


/**
 * URL: `gtm:/home`
 * 
 * @param props 
 */
const PageHome: React.FC<Props> = (props) => {
  const { routes, navi } = props;

  const main_urls = [
    "gtm:/test4",
    "gtm:/dev",
  ];

  const header = (
    <div key={1} className="topBar">
      <div className="page-home__header__left">
        Home
      </div>
    </div>
  );

  const content = (
    <div key={2}>
      <div className="page-home__content__mainPages">
        Main pages
        <div className="page-home__content__mainPages__pages">
          {main_urls.map(url => {
            const route = routes.find(route => route.url === url);
            assert(typeof route !== "undefined");
            return (
              <button key={route.url} onClick={() => { navi.setURL(route.url);  }}>{route.name}</button>
            );
          })}
        </div>
      </div>
    </div>
  );

  const footer = (
    <div key={3} className="bottomBar page-home__footer">
      <button>STOP ALL RUNNING TASKS</button>
    </div>
  );


  return (
    <HCFLayout>
      {header}
      {content}
      {footer}
    </HCFLayout>
  );
}


export default PageHome;
