/**
 * Asserts the condition `condition`.  If the value is not boolean then it is
 * cast to a boolean value.  If the condition evaluates to a falsy value then
 * an exception is raised.  An optional message `message` is displayed and
 * other optional things `...other` printed to the error console.
 *
 * @param {any} condition
 * @param {string=} message
 * @param {any=} other
 *
 * @throws Will throw an error if `condition` is falsy.
 */
export function assert(condition: any, messageOrCallback: string | (() => any) = "Assertion failed", callback?: () => any): asserts condition {
  if (!condition) {
    if (typeof messageOrCallback === "string") {
      let errorMessage = messageOrCallback;
      if (typeof callback === "function") {
        const potentialErrorMessage = callback();
        if (typeof potentialErrorMessage === "string") {
          errorMessage = potentialErrorMessage;
        }
      }
      throw new Error(errorMessage);
    } else if (typeof messageOrCallback === "function") {
      let errorMessage = "Assertion failed";
      const potentialErrorMessage = messageOrCallback();
      if (typeof potentialErrorMessage === "string") {
        errorMessage = potentialErrorMessage;
      }
      assert(typeof callback === "undefined", `Assertion argument error: don't supply two callbacks!`);
      throw new Error(errorMessage);
    } else {
      console.error("Assertion argument error:", { condition, messageOrCallback });
      throw new Error(`Assertion argument error: condition: ${condition}, messageOrCallback: ${messageOrCallback}`);
    }
  }
}

/**
 *
 * @param {any[]} validMembers
 */
export function createMembershipTestFunction(validMembers: any[]) {
  let s: Set<any>;

  if (Array.isArray(validMembers)) {
    s = new Set(validMembers);
  } else {
    throw new Error("Not supported!");
  }

  return function(potentialMember: any) {
    return s.has(potentialMember);
  };
}

export function isObject(x: any) {
  return x !== null && typeof x === "object";
}

export function isArray(x: any) {
  return Array.isArray(x);
}

/**
 * Parses URL `url`.
 *
 * TODO: I'm not sure whether or not I need to use `encodeURI()` or
 * `encodeURIComponent()` here.
 *
 * @param {string} url
 *
 * @throws Will throw an error if the URL `url` is invalid.
 */
export function parseURL(url: string) {
  const urlObj = new URL(url);

  const {
    hash,
    host,
    hostname,
    href,
    origin,
    password,
    pathname,
    port,
    protocol,
    search,
    username,
  } = urlObj;

  const parsed_url = {
    // From URL
    hash,
    host,
    hostname,
    href,
    origin,
    password,
    pathname,
    port,
    protocol,
    search,
    username,
    // Other
    authority: "",
    fragment: "",
    query: "",
    userinfo: "",
    scheme: "",
    path: "",
    pathSegments: [] as string[],
    slug: "",
    queryPairs: [] as [string, string][],
    queryObject: {} as { [key: string]: string },
    url,
  };

  // protocol
  if (protocol.length > 0) {
    parsed_url.scheme = protocol.substring(0, protocol.length - 1);
  }

  // userinfo
  if (username.length > 0 || password.length > 0) {
    if (username.length > 0 && password.length === 0) {
      parsed_url.userinfo = username;
    } else if (username.length > 0 && password.length > 0) {
      parsed_url.userinfo = `${username}:${password}`;
    } else if (username.length === 0 && password.length > 0) {
      parsed_url.userinfo = `:${password}`;
    } else {
      throw new Error();
    }
  }

  // query
  parsed_url.query = search.substring(1, search.length);

  // fragment
  parsed_url.fragment = hash.substring(1, hash.length);

  // path segments
  parsed_url.pathSegments = pathname.split("/");
  parsed_url.pathSegments.shift();

  parsed_url.slug = parsed_url.pathSegments[parsed_url.pathSegments.length - 1];

  if (parsed_url.userinfo.length > 0 || parsed_url.port.length > 0) {
    if (parsed_url.hostname.length === 0) {
      throw new Error();
    }

    const { userinfo } = parsed_url;

    if (userinfo.length > 0 && port.length > 0) {
      parsed_url.authority = `${userinfo}@${hostname}:${port}`;
    } else if (userinfo.length > 0 && port.length === 0) {
      parsed_url.authority = `${userinfo}@${hostname}`;
    } else if (userinfo.length === 0 && port.length > 0) {
      parsed_url.authority = `${hostname}:${port}`;
    } else {
      parsed_url.authority = hostname;
    }
  }

  // query_pairs & query_object
  for (const pair of urlObj.searchParams.entries()) {
    parsed_url.queryPairs.push(pair);
    const [key, value] = pair;
    parsed_url.queryObject[key] = value;
  }

  // path
  parsed_url.path = pathname;

  return parsed_url;
}

/**
 *
 * @param {string} str
 */
export function copyToClipboard(str: string) {
  const el = document.createElement("textarea");
  el.value = str;
  document.body.appendChild(el);
  el.select();
  document.execCommand("copy");
  document.body.removeChild(el);
}

// Throttle call to function
export function debounce(this: any, fn: () => void, ms: number) {
  let timer: any;
  return () => {
    clearTimeout(timer);
    timer = setTimeout(() => {
      timer = null;
      fn.apply(this, arguments as any);
    }, ms);
  };
}

/**
 *
 * @param {string} filename
 * @param {string} text
 */
export function download(filename: string, text: string) {
  var element = document.createElement("a");
  element.setAttribute(
    "href",
    "data:text/plain;charset=utf-8," + encodeURIComponent(text)
  );
  element.setAttribute("download", filename);

  element.style.display = "none";
  document.body.appendChild(element);

  element.click();

  document.body.removeChild(element);
}

/**
 * Encodes the string `s` to be used in regular expressions, such that it does
 * not conflict with any regular expression meta-characters.
 *
 * @param {string} s
 */
export function encodeForRegex(s: string) {
  // $& means the whole matched string
  return s.replace(/[.*+?^${}()|[\]\\]/g, "\\$&");
}

const s1 = new Set(["has", "starts-width", "ends-with", "not-starts-with"]);

/**
 *
 * @param {string} query
 * @param {string} mode "has", "starts-width", "ends-with", "not-starts-with"
 * @param {boolean} caseSensitive
 */
export function createSearchFilter(query: string, mode: string, caseSensitive: boolean) {
  assert(s1.has(mode));
  assert(typeof caseSensitive === "boolean");

  if (caseSensitive) {
    // is case sensitive, do nothing
  } else {
    query = query.toLocaleLowerCase();
  }

  const escaped_query = encodeForRegex(query);

  let regex = null;

  if (mode === "has") {
    regex = new RegExp(`.*${escaped_query}.*`);
  } else if (mode === "starts-width") {
    regex = new RegExp(`^${escaped_query}.*`);
  } else if (mode === "ends-with") {
    // TODO: I'm not sure if this one is correct.  Double check later.
    regex = new RegExp(`.*${escaped_query}$`);
  } else if (mode === "not-starts-with") {
    regex = new RegExp(`.+${escaped_query}.*`);
  } else {
    throw new Error();
  }

  if (!(regex instanceof RegExp)) {
    throw new Error();
  }

  const reg = regex;

  if (caseSensitive) {
    /**
     *
     * @param {string} string
     */
    const f = (string: string) => {
      return string.match(reg) !== null;
    };
    return f;
  } else {
    /**
     *
     * @param {string} string
     */
    const f = (string: string) => {
      return string.toLocaleLowerCase().match(reg) !== null;
    };
    return f;
  }
}

/**
 * a u b
 *
 * @param {Set<any>} a
 * @param {Set<any>} b
 */
export function getSetUnion(a: Set<any>, b: Set<any>) {
  const union = new Set([...a, ...b]);
  return union;
}

/**
 * a n b
 *
 * @param {Set<any>} a
 * @param {Set<any>} b
 */
export function getSetIntersection(a: Set<any>, b: Set<any>) {
  const intersection = new Set([...a].filter((x) => b.has(x)));
  return intersection;
}

/**
 * a - b OR a \ b
 *
 * @param {Set<any>} a
 * @param {Set<any>} b
 */
export function getSetDifference(a: Set<any>, b: Set<any>) {
  const difference = new Set([...a].filter((x) => !b.has(x)));
  return difference;
}

/**
 *
 * @param {Set<any>} a
 * @param {Set<any>} b
 */
export function getSetSymmetricDifference(a: Set<any>, b: Set<any>) {
  const x = getSetDifference(a, b);
  const y = getSetDifference(b, a);
  return getSetUnion(x, y);
}

/**
 *
 * @param {string[]} strings
 * @param {string} query
 */
export function filterStrings(strings: string[], query: string) {
  query = query.trim().toLowerCase();

  const escaped_query = encodeForRegex(query);

  if (escaped_query.length === 0) {
    return strings;
  }

  // const startsWithRegex = new RegExp(`^${escaped_query}.*`);
  // const notStartsHasRegex = new RegExp(`.+${escaped_query}.*`);
  const hasRegex = new RegExp(`.*${escaped_query}.*`);

  strings = strings.filter((string) => {
    return string.toLowerCase().match(hasRegex) !== null;
  });

  return strings;
}

export const formatMS = (function() {
  const msInMillisecond = 1;
  const msInSecond = 1000 * msInMillisecond;
  const msInMinute = 60 * msInSecond;
  const msInHour = 60 * msInMinute;
  const msInDay = 24 * msInHour;

  function format2TS(x: number) {
    if (x < 10) {
      return `0${x}`;
    } else {
      return x;
    }
  }

  function format4TS(x: number) {
    if (x < 10) {
      return `00${x}`;
    } else if (x < 100) {
      return `0${x}`;
    } else {
      return x;
    }
  }

  function formatMS(ms: number) {
    // Remaining milliseconds
    let rms = ms;

    const days = Math.floor(rms / msInDay);
    rms = rms - days * msInDay;
    const hours = Math.floor(rms / msInHour);
    rms = rms - hours * msInHour;
    const minutes = Math.floor(rms / msInMinute);
    rms = rms - minutes * msInMinute;
    const seconds = Math.floor(rms / msInSecond);
    rms = rms - seconds * msInSecond;
    const milliseconds = Math.floor(rms / msInMillisecond);

    if (ms === 0) {
      return "0";
    } else if (ms < 1000 * 60) {
      // < 1 minute
      // 32.781

      return `${seconds}.${format4TS(milliseconds)}`;
    } else if (ms < 1000 * 60 * 60) {
      // < 1 hour
      // 01:75.834
      return `${minutes}:${format2TS(seconds)}.${format4TS(milliseconds)}`;
    } else if (ms < 1000 * 60 * 60 * 24) {
      // < 1 day
      // 03:15:75.743

      return `${hours}:${format2TS(minutes)}:${format2TS(seconds)}.${format4TS(
        milliseconds
      )}`;
    } else {
      // >= 1 day
      // 3 01:75:

      return `${days} ${format2TS(hours)}:${format2TS(minutes)}:${format2TS(
        seconds
      )}.${format4TS(milliseconds)}`;
    }
  }

  return formatMS;
})();

/**
 * Deep mixing from `objectSource` to `objectTarget`.
 *
 * @param {any} source
 * @param {any} target
 */
export function deepMixIn(source: any, target: any) {
  assert(isObject(source) || isArray(source));
  assert(isObject(target) || isArray(target));

  if (isObject(source)) {
    assert(isObject(target));
  } else if (isArray(source)) {
    assert(isArray(target));
  } else {
    throw new Error("Shouldn't happen");
  }

  if (isArray(source)) {
    throw new Error("Array mixing not implemented");
  } else if (isObject(source)) {
    Object.keys(source).forEach((source_prop_name) => {
      const source_prop = source[source_prop_name];
      if (target.hasOwnProperty(source_prop_name)) {
        const target_prop = target[source_prop_name];

        if (isObject(source_prop)) {
          if (isObject(target_prop)) {
            // Mix even deeper
            deepMixIn(source_prop, target_prop);
          } else {
            // Replace target prop with source prop
            target[source_prop_name] = source_prop;
          }
        } else if (isArray(source_prop)) {
          if (isArray(target_prop)) {
            throw new Error("Not implemented");
          } else {
            // Replace target prop with source prop
            target[source_prop_name] = source_prop;
          }
        } else {
          // Replace
          target[source_prop_name] = source_prop;
        }
      } else {
        target[source_prop_name] = source_prop;
      }
    });
  }
}
