import React from 'react';
import ReactDOM from 'react-dom';

import App from "./App";

import { createNavi } from './navi';
import { getGTMNativeApp } from "./gtm-app/gtm";

import "./index.css";


async function main() {
  const database_name = "testdb";
  const gtm = await getGTMNativeApp(database_name);
  (window as any).gtm = gtm;

  const navi = createNavi("gtm:/home");

  ReactDOM.render((
    <App navi={navi} gtm={gtm} />
  ), document.getElementById('root'));
}


main().catch((reason) => {
  console.error((reason));
  throw new Error(reason);
});

