import React, { useState, useEffect } from "react";

import PlayButton from "../play-button";

import { formatMS } from "../../utils-ui";
import { Navi } from '../../navi';

import { GTMApp } from "../../gtm-app/gtm";

import "./index.css";

interface IProps {
  gtm: GTMApp;
  navi: Navi;
  task_id: string,
  buttonEnterText?: string,
  buttonEnterOnClick?: () => void,
  buttonEnterHide?: boolean
}

const TaskListItem: React.FC<IProps> = (props) => {
  const { 
    task_id, 
    gtm, 
    navi,
    buttonEnterText = "o",
    buttonEnterOnClick = () => {
      navi.setURL(`gtm:/tasklist?id=${task_id}`)
    },
    buttonEnterHide = false
  } = props;

  const task = gtm.task.getTask(task_id);

  const n = 30;

  const short_description =
    task.description.length <= n
      ? task.description
      : task.description.substring(0, n - 3) + "...";

  

  // TODO: reimplement


  let active = gtm.task.getActiveTaskIDs().includes(task_id);
  

  const work_time = gtm.task.getTaskTotalWorktime(task_id);

  const [, setDate] = useState(new Date());

  // Replaces componentDidMount and componentWillUnmount
  useEffect(() => {
    const timerID = setTimeout(() => {
      tick();
    }, 1);

    return function cleanup() {
      clearInterval(timerID);
    };
  });

  function tick() {
    setDate(new Date());
  }

  return (
    <div className={task.status === "open" ? "task-list-item" : "task-list-item task-list-item--complete"}>
      <div style={{  display: "flex", flexDirection: "row", justifyContent: "flex-start" }}>
        <button
          style={{ paddingLeft: "10px", paddingRight: "10px" }}
          onClick={() => {
            navi.setURL(`gtm:/task/${task_id}?mode=edit`);
          }}
        >...</button>
        <PlayButton
          active={active}
          onChange={(active) => {
            if (active) {
              // TODO: maybe change in future!
              gtm.task.stopAllTasks();
              gtm.task.startTask(task_id);
            } else {
              gtm.task.stopAllTasks();
            }
          }}
        ></PlayButton>
        <div style={{ paddingLeft: "2px", paddingRight: "2px", alignSelf: "center", fontSize: "0.9rem" }}>{short_description}</div>
      </div>
      
      <div style={{ display: "flex", flexDirection: "row", justifyContent: "flex-end", alignItems: "center" }}>
        <span style={{ paddingRight: "2px", fontSize: "0.75rem" }}>{formatMS(work_time)}</span>
        {
          buttonEnterHide ? null : <button onClick={buttonEnterOnClick}>{buttonEnterText}</button>
        }
      </div>
    </div>
  );
}

export default TaskListItem;
