import React, { useRef, useEffect, useLayoutEffect, useState } from "react";

import "./index.css";

interface IProps {}


function render(canvas: HTMLCanvasElement) {
  const ctx = canvas.getContext("2d");
  if (ctx === null) return;

  ctx.fillRect(0, 0, 10, 10);
}


const GanttView: React.FC<IProps> = (props) => {

  const [width, setWidth] = useState(0);
  const [height, setHeight] = useState(0);
  
  
  const canvas_ref = useRef<HTMLCanvasElement>(null);

  // responsive width and height
  useEffect(() => {
    const target_ref = canvas_ref;
    const target = target_ref.current;
    if (target === null) return;

    console.info("yo?");

    target.width = target.clientWidth;
    target.height = target.clientHeight;

    setWidth(target.clientWidth);
    setHeight(target.clientHeight);
  }, []);

  useLayoutEffect(() => {


    const canvas = canvas_ref.current;

    if (canvas === null) return;

    console.info(canvas.width, canvas.height);

    const ctx = canvas.getContext('2d');

    if (ctx === null) return;

    // some canvas stuff..
    ctx.beginPath()
    ctx.moveTo(0,height/2)
    ctx.lineTo(width, height/2)
    ctx.stroke()

    render(canvas);

  }, [width, height]);


  return <canvas className="gant-view" ref={canvas_ref}/>;
}

export default GanttView;


