import React from "react";

import HCFLayout from "../../layout/hcf";
import { Navi } from '../../navi';
import { Routes } from '../../App';

import { GTMApp } from "../../gtm-app/gtm";
import { assert } from "../../utils-ui";

import "./index.css";

interface Props {
  gtm: GTMApp;
  navi: Navi;
  routes: Routes;
}

function getRoute(routes: Routes, url: string) {
  const route = routes.find(route => route.url === url);
  assert(typeof route !== "undefined");
  return route;
}


/**
 * URL: `gtm:/dev`
 * 
 */
const PageDev: React.FC<Props> = (props) => {

  const other_urls = [    
    "gtm:/tasklist", 
    "gtm:/gantt",
  ];

  const { navi, routes } = props;

  const sub_routes: Routes = [];

  routes.forEach((route) => {
    const { url } = route;
    if (url.startsWith(navi.url) && url !== navi.url) {
      sub_routes.push(route);
    }
  });

  const header = (
    <div key={1} className="topBar">
      <button onClick={() => { navi.back(); }}>Go back</button>
      Developer page
    </div>
  );

  const content = (
    <div key={2} style={{ display: "flex", flexDirection: "column" }}>
      About
      {(() => {
        const url = "gtm:/about";
        const route = getRoute(routes, url);
        return (
          <button key={url} onClick={() => { navi.setURL(url); }}>{route.name}</button>
        );
      })()}
      Import / import
      {(() => {
        const url = "gtm:/import";
        const route = getRoute(routes, url);
        return (
          <button key={url} onClick={() => { navi.setURL(url); }}>{route.name}</button>
        );
      })()}
      {(() => {
        const url = "gtm:/export";
        const route = getRoute(routes, url);
        return (
          <button key={url} onClick={() => { navi.setURL(url); }}>{route.name}</button>
        );
      })()}
      Reload:
      <button onClick={() => {
        location.reload(false); // eslint-disable-line no-restricted-globals
      }}>Reload (from cache)</button>
      <button onClick={() => {
        location.reload(true); // eslint-disable-line no-restricted-globals
      }}>Reload (ignore cache)</button>
      Routes:
      {sub_routes.map((sub_route) => {
        const { name, url } = sub_route;
        return (
        <button key={url} onClick={() => { navi.setURL(url); }}>{name}</button>
        );
      })}
      Other:
      <button onClick={() => { throw new Error(); }}>Throw error</button>
      Other pages:
      {other_urls.map(url => {
        const route = getRoute(routes, url);
        return (
          <button key={url} onClick={() => { navi.setURL(url); }}>{route.name}</button>
        );
      })}

    </div>
  );

  /*
  const footer = (
    <div key={3} className="bottomBar">
      Footer: bla bla bla
    </div>
  );
  */
  
  return (
    <HCFLayout>
      {header}
      {content}
      {null}
    </HCFLayout>
  );
}

export default PageDev;

