import React, { useState, useRef } from "react";

import HCFLayout from "../../layout/hcf";
import TextArea from "../../component/textarea";
import { Navi } from '../../navi';
import { GTMApp } from "../../gtm-app/gtm";
import { assert } from "../../utils-ui";

import "./index.css";

type Props = {
  gtm: GTMApp,
  navi: Navi
}

/**
 * URL: `gtm:/import`
 * 
 * @param props 
 */
const PageImport: React.FC<Props> = (props) => {
  const { gtm, navi } = props;

  const fileInput = useRef(null);

  const [text, setText] = useState("");

  function onChange(e: any) {
    setText(e.target.value.trim());
  }

  function handleFileLoaded(e: ProgressEvent<FileReader>) {
    if (e.target === null) return;
    const text = "" + e.target.result;
    performImport(text);
  }

  function handleFileInputChange(e: any) {
    assert(e.target.files.length === 1);
    const file = e.target.files[0];
    const reader = new FileReader()
    reader.onload = handleFileLoaded;
    reader.onerror = error => {
      throw new Error();
    }
    reader.readAsText(file)
  }

  function performImport(text: string) {
    gtm.common.doImport(text);
    navi.setURL("gtm:/home");
  }

  // ---------------------------------------------------------------------------
  // Header
  const header = React.createElement(
    "div",
    {
      key: "1",
      className: "topBar",
    },
    React.createElement(
      "div",
      {},
      React.createElement(
        "button",
        {
          onClick: () => {
            navi.back();
          },
        },
        "Go back"
      )
    )
  );

  // ---------------------------------------------------------------------------
  // Content

  const content = (
    <div key={2} style={{
      display: "flex",
      flexDirection: "column",
      flexGrow: 1,
      height: "100%",
    }}>
      <TextArea value={text} onChange={onChange} style={{
        width: "100%",
        display: "flex",
        flexGrow: 1,
        resize: "none",
      }}></TextArea>
    </div>
  );


  // ---------------------------------------------------------------------------
  // Footer

  const footer = (
    <div key={3} style={{ display: "flex "}} className="bottomBar">
      <button onClick={() => {
        performImport(text);
      }}>Import</button>
      <label style={{ 
        textDecoration: "none",
        display: "inline-block",
        padding: "var(--theme__button__padding)",
        color: "var(--theme__button__color)",
        border: "var(--theme__button__border)",
        borderRadius: "var(--theme__button__border-radius)",
        margin: "2px 2px 2px 2px",
        boxShadow: "0px 0px 4px rgba(0, 0, 0, 0.241)",
        transition: "var(--theme__button__transition)",
        backgroundColor: "var(--theme__button__background-color)",
        fontSize: "0.8rem",
        lineHeight: "normal",
        }}
        
        >Import file<input type="file" ref={fileInput} style={{ display: "none" }} onChange={handleFileInputChange} /></label>
      
    </div>
  );

  return (
    <HCFLayout>
      {header}
      {content}
      {footer}
    </HCFLayout>
  );
}


export default PageImport;
