import React, { useState } from "react";

import HCFLayout from "../../layout/hcf";
import { Navi } from '../../navi';

import { GTMApp } from "../../gtm-app/gtm";

import "./index.css";



interface Props {
  gtm: GTMApp;
  navi: Navi;
}


/**
 * URL: `gtm:/dev/server`
 * 
 * @param props 
 */
const PageDevServer: React.FC<Props> = (props) => {
  const { navi, gtm } = props;

  const [text, setText] = useState("http://");


  const header = (
    <div key={1} className="topBar">
      <div>
        <button onClick={() => { navi.back(); }}>Go back</button>
      </div>
    </div>
  );

  const content = (
    <div key={2}>
      URL:
      <input
        type="text"
        value={text}
        onChange={(e) => {
          setText(e.target.value);
        }}
      ></input>
      <button
        onClick={() => {
          const url = text;
          
          const data = gtm.common.doExport();
          fetch(url, {
            method: 'POST', // or 'PUT'
            body: data, // data can be `string` or {object}!
            headers: {
              'Content-Type': 'application/json'
            }
          }).then((value) => {}).catch((reason) => {
            console.error(reason);
          });
        }}
      >
        Send data
      </button>
    </div>
  );
  
  /*
  const footer = (
    <div key={3} className="bottomBar">
      ...
    </div>
  );
  */

  return (
    <HCFLayout>
      {header}
      {content}
      {null}
    </HCFLayout>
  );
}


export default PageDevServer;
