import { parseURL, assert } from "./utils-ui";


type URLObject = ReturnType<typeof parseURL>;

export type Navi = ReturnType<typeof createNavi>;

// TODO: add init value so I don't always have to check for null!

/**
 * Creates a `Nav` object.
 *
 * @returns {{ init: (url: string) => void, back: () => void, go: (offset: number) => void, forward: () => void, setURL: (url: string) => void, reload: () => void, url: string, urlObj: any, initialized: boolean, callback?: () => void }}
 */
export function createNavi(initialURL: string) {

  // Initialize
  let current_url: string = initialURL;
  let current_url_obj: URLObject = parseURL(initialURL);
  const history: URLObject[] = [current_url_obj];
  let history_index = 0;

  /**
   *
   * @param {number} offset
   * @throws
   */
  function go(offset: number) {
    const effective_index = history_index + offset;
    assert(effective_index >= 0 && effective_index < history.length);
    history_index = effective_index;
    _update();
  }

  /**
   *
   */
  function back() {
    go(-1);
  }

  /**
   *
   */
  function forward() {
    go(1);
  }

  /**
   *
   */
  function reload() {
    go(0);
  }

  /**
   *
   * @param {string} url
   *
   * @throws
   */
  function setURL(url: string, pop = 0) {
    const url_obj = parseURL(url);

    history_index = history_index - pop;

    // Pop of history
    while (history.length - 1 > history_index) {
      history.pop();
    }
    history.push(url_obj);
    history_index++;
    _update();
  }

  /**
   *
   */
  function _update() {
    current_url_obj = history[history_index];
    current_url = current_url_obj.url;

    return_object.url = current_url;
    return_object.urlObj = current_url_obj;
    if (typeof return_object.callback === "function") {
      return_object.callback();
    }
  }

  type ReturnObject = {
    back: typeof back,
    go: typeof go,
    forward: typeof forward,
    setURL: typeof setURL,
    reload: typeof reload,
    url: string,
    urlObj: URLObject,
    callback: null | (() => void),
  };

  const return_object: ReturnObject = {
    back,
    go,
    forward,
    setURL,
    reload,
    url: current_url,
    urlObj: current_url_obj,
    callback: null,
  };

  return return_object;
}
