import React from "react";

import TextArea from "../../component/textarea";
import PlayButton from "../../component/play-button";

import HCFLayout from "../../layout/hcf";


import { assert, formatMS } from "../../utils-ui";
import { Navi } from '../../navi';

import { GTMApp } from "../../gtm-app/gtm";

import "./index.css";




interface Props {
  gtm: GTMApp;
  navi: Navi;
}

interface State {
  url: string;
  description: string;
  id: string;
  toggle_remove_dependencies: boolean;
  toggle_remove_dependents: boolean;
  comments: string[];


  to_delete: boolean;
  animate: boolean;
  animate_id: any;
  _i: number;
}



/**
 * URL: `gtm:/task`
 */
export default class PageEditTask extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);

    const { gtm, navi } = this.props;

    const id = navi.urlObj.pathSegments[1];

    assert(typeof id === "string");

    const has_task = gtm.task.hasTask(id);

    const state: State = {
      url: navi.url,
      description: "",
      id,
      toggle_remove_dependencies: false,
      toggle_remove_dependents: false,
      // NOTE: probably slow
      comments: [],
      to_delete: false,
      animate: false,
      animate_id: null,
      _i: 0
    };

    if (has_task) {
      const task = gtm.task.getTask(id);
      state.description = task.description;
      state.comments = [...task.comments];
    }

    this.state = state;

    if (!has_task) {
      navi.back();
    }

    this.handleChangeDescription = this.handleChangeDescription.bind(this);
    this.handleCommentChange = this.handleCommentChange.bind(this);
    this.animationCallback = this.animationCallback.bind(this);
  }

  animationCallback() {
    this.setState({ _i: this.state._i + 1 });
  }

  updateState() {

    const { gtm, navi } = this.props;

    const id = navi.urlObj.pathSegments[1];

    if (!gtm.task.hasTask(id)) {
      navi.back();
      return;
    }

    if (navi.urlObj === null) {
      // type guard
      throw new Error();
    }

    const task = gtm.task.getTask(id);

    this.setState({
      description: task.description,
      id,
      // NOTE: probably slow
      comments: [...task.comments],
      to_delete: false,
    });
  }

  componentDidUpdate(prevProps: any, prevState: any, snapshot: any) {

    const { navi } = this.props;
    const { animate, animate_id } = this.state;
    if (navi.url !== prevState.url) {
      if (navi.url === null) {
        // type guard
        throw new Error();
      }
      // WARNING: hmm...
      clearInterval(animate_id);
      this.setState({ url: navi.url, animate: false, animate_id: null });
      this.updateState();
    } else if (animate && animate_id === null) {
      // Start animating
      const animate_id = setInterval(this.animationCallback, 100);
      this.setState({ animate: true, animate_id: animate_id });
    } else if (!animate && animate_id !== null) {
      // Stop animating
      clearInterval(animate_id);
      this.setState({ animate: false, animate_id: null });
    }
  }

  componentWillUnmount() {
    clearInterval(this.state.animate_id);
  }

  forceRerender() {
    const { _i } = this.state;
    this.setState({ _i: _i + 1 });
  }

  forceStatefulRerender() {
    const { state } = this;
    this.setState({ ...state });
  }

  handleCommentChange(new_comment_text: string, comment_index: number) {
    const { gtm } = this.props;
    const { id } = this.state;

    if (!gtm.task.hasTask(id)) {
      return;
    }

    gtm.task.changeComment(id, comment_index, new_comment_text)

    const task = gtm.task.getTask(id);

    this.setState({
      comments: [...task.comments],
    });
  }

  handleChangeDescription(e: any) {
    const { gtm } = this.props;
    const { id } = this.state;

    if (!gtm.task.hasTask(id)) {
      return;
    }

    const new_description = e.target.value;

    gtm.task.changeDescription(id, new_description)

    this.setState({
      description: new_description,
    });
  }

  render() {

    const { id } = this.state;
    const { gtm, navi } = this.props;

    


    if (!gtm.task.hasTask(id)) {
      navi.back();
      return (<div>...</div>);
    }

    const task = gtm.task.getTask(id);

    const { description } = task;

    // Open dependents
    const dependents_open: string[] = [];

    // Non-open dependents
    const dependents_nonopen: string[] = [];

    {
      const deps = task.dependents;
      const deps_open = dependents_open;
      const deps_nonopen = dependents_nonopen;

      deps.forEach((dep_id) => {
        const dep_task = gtm.task.getTask(dep_id);
        if (dep_task.status === "open") {
          deps_open.push(dep_id);
        } else {
          deps_nonopen.push(dep_id);
        }
      });
    }

    // Open dependencies
    const dependencies_open: string[] = [];

    // Non-open dependencies
    const dependencies_nonopen: string[] = [];

    {
      const deps = task.dependencies;
      const deps_open = dependencies_open;
      const deps_nonopen = dependencies_nonopen;

      deps.forEach((dep_id) => {
        const dep_task = gtm.task.getTask(dep_id);
        if (dep_task.status === "open") {
          deps_open.push(dep_id);
        } else {
          deps_nonopen.push(dep_id);
        }
      });
    }

    const is_open = task.status === "open";

    
    


    const self_work_time = gtm.task.getTaskSelfWorktime(id);
    const total_work_time = gtm.task.getTaskTotalWorktime(id);

    const header = (
      <div key={1} className="topBar page-edit-task__header">
        <div>
          <button onClick={() => { navi.back(); }}>
            Go back
          </button>
          <span>{`ID: ${task.id}`}</span>
        </div>
        <div>
          {
            this.state.to_delete ? (
              <button onClick={() => {
                gtm.task.deleteTask(id);
                navi.back();
              }}>
                Confirm deletion
              </button>
            ) : (
                <button onClick={() => { this.setState({ to_delete: true }) }}>Delete</button>
              )
          }
        </div>
        <div>
          <button onClick={() => { navi.setURL("gtm:/tasklist/"); }}>
            Go to task list
          </button>
        </div>
      </div>
    );


    // Content
    const content = React.createElement(
      "div",
      {
        key: "2",
      },
      <div style={{ border: "1px solid var(--missing)" }}>
        Date created: {new Date(task.date_created).toUTCString()}
      </div>,
      <div style={{ border: "1px solid var(--missing)" }}>
        Date modified: {new Date(task.date_modified).toUTCString()}
      </div>,
      <div style={{ border: "1px solid var(--missing)" }}>
        Work time (self): {formatMS(self_work_time)} s
      </div>,
      <div style={{ border: "1px solid var(--missing)" }}>
        Work time (total): {formatMS(total_work_time)} s
      </div>,
      <div className="page-edit-task__1">
        <div className="page-edit-task__2">
          Description
        </div>
        <div className="page-edit-task__4">
          <TextArea className="page-edit-task__3" value={description} onChange={this.handleChangeDescription} rows={12}></TextArea>
        </div>
      </div>,
      // -----------------------------------------------------------------------
      // Dependents
      //
      //
      React.createElement(
        "div",
        {
          style: {
            display: "flex",
            flexDirection: "column",

            marginTop: "var(--theme__task-edit-entry__margin-top)",
            marginBottom: "var(--theme__task-edit-entry__margin-bottom)",

            paddingBottom: "var(--theme__task-edit-entry__padding-bottom)",
            paddingLeft: "var(--theme__task-edit-entry__padding-left)",
            paddingRight: "var(--theme__task-edit-entry__padding-right)",

            border: "var(--theme__task-edit-entry__border)",
            boxShadow: "var(--theme__task-edit-entry__box-shadow)",
          },
        },
        React.createElement(
          "div",
          {
            style: {
              display: "flex",
              flexDirection: "column",

              paddingTop: "var(--theme__task-edit-entry-header__padding-top)",
              paddingBottom:
                "var(--theme__task-edit-entry-header__padding-bottom)",

              marginBottom:
                "var(--theme__task-edit-entry-header__margin-bottom)",

              backgroundColor:
                "var(--theme__task-edit-entry-header__background-color)",

              boxShadow: "var(--theme__task-edit-entry-header__box-shadow)",
            },
          },
          React.createElement(
            "div",
            {
              style: {
                marginTop: "4px",
                marginBottom: "8px",
                fontSize: "0.9rem",
                fontWeight: "bold",
              },
            },
            // Dependents
            "Supertasks"
          ),
          React.createElement(
            "div",
            {
              style: {
                display: "flex",
                flexDirection: "row",
                justifyContent: "space-between",
              },
            },
            React.createElement(
              "button",
              {
                onClick: () => {
                  navi.setURL(
                    `gtm:/edit-depend?${[
                      ["id", id],
                      ["type", "dependent"],
                      ["mode", "add"],
                    ]
                      .map((x) => x.map((y) => escape(y)).join("="))
                      .join("&")}`
                  );
                },
              },
              "Add"
            ),
            React.createElement(
              "button",
              {
                onClick: () => {
                  navi.setURL(
                    `gtm:/edit-depend?${[
                      ["id", id],
                      ["type", "dependent"],
                      ["mode", "remove"],
                    ]
                      .map((x) => x.map((y) => escape(y)).join("="))
                      .join("&")}`
                  );
                },
              },
              "Remove"
            )
          )
        ),

        React.createElement(
          "span",
          { style: { fontSize: "0.8rem", marginBottom: "4px" } },
          "Open"
        ),
        React.createElement(
          "div",
          {
            style: {
              display: "flex",
              flexDirection: "row",
              flexWrap: "wrap",
            },
          },
          dependents_open.map((dep_id) => {
            
            const task = gtm.task.getTask(dep_id);

            const n = 20;

            const description_short =
              task.description.length <= n
                ? task.description.substring(0, n)
                : task.description.substring(0, n - 3) + "...";

            const button_text = `${dep_id}: ${description_short}`;

            return React.createElement(
              "button",
              {
                key: dep_id,
                onClick: () => {
                  console.info(`Goto: gtm:/task/${dep_id}`);
                  navi.setURL(`gtm:/task/${dep_id}`);
                },
              },
              button_text
            );
          })
        ),

        // Non-open dependencies
        React.createElement(
          "span",
          { style: { fontSize: "0.8rem", marginBottom: "4px" } },
          "Non-open"
        ),
        React.createElement(
          "div",
          {
            style: {
              display: "flex",
              flexDirection: "row",
              flexWrap: "wrap",
            },
          },
          dependents_nonopen.map((dep_id) => {
            const task = gtm.task.getTask(dep_id);

            const n = 20;

            const description_short =
              task.description.length <= n
                ? task.description.substring(0, n)
                : task.description.substring(0, n - 3) + "...";

            const button_text = `${dep_id}: ${description_short}`;

            return React.createElement(
              "button",
              {
                key: dep_id,
                onClick: () => {
                  console.info(`Goto: gtm:/task/${dep_id}`);
                  navi.setURL(`gtm:/task/${dep_id}`);
                },
              },
              button_text
            );
          })
        )
      ),

      // -------------------------------------------------------------------------
      // Dependencies
      React.createElement(
        "div",
        {
          style: {
            display: "flex",
            flexDirection: "column",

            marginTop: "var(--theme__task-edit-entry__margin-top)",
            marginBottom: "var(--theme__task-edit-entry__margin-bottom)",

            paddingBottom: "var(--theme__task-edit-entry__padding-bottom)",
            paddingLeft: "var(--theme__task-edit-entry__padding-left)",
            paddingRight: "var(--theme__task-edit-entry__padding-right)",

            border: "var(--theme__task-edit-entry__border)",
            boxShadow: "var(--theme__task-edit-entry__box-shadow)",
          },
        },
        React.createElement(
          "div",
          {
            style: {
              display: "flex",
              flexDirection: "column",

              paddingTop: "var(--theme__task-edit-entry-header__padding-top)",
              paddingBottom:
                "var(--theme__task-edit-entry-header__padding-bottom)",

              marginBottom:
                "var(--theme__task-edit-entry-header__margin-bottom)",

              backgroundColor:
                "var(--theme__task-edit-entry-header__background-color)",

              boxShadow: "var(--theme__task-edit-entry-header__box-shadow)",
            },
          },
          React.createElement(
            "div",
            {
              style: {
                marginTop: "4px",
                marginBottom: "8px",
                fontSize: "0.9rem",
                fontWeight: "bold",
              },
            },
            "Subtasks"
            //"Dependencies"
          ),
          React.createElement(
            "div",
            {
              style: {
                display: "flex",
                flexDirection: "row",
                justifyContent: "space-between",
              },
            },
            React.createElement(
              "button",
              {
                onClick: () => {
                  navi.setURL(
                    `gtm:/edit-depend?${[
                      ["id", id],
                      ["type", "dependency"],
                      ["mode", "add"],
                    ]
                      .map((x) => x.map((y) => escape(y)).join("="))
                      .join("&")}`
                  );
                },
              },
              "Add"
            ),
            React.createElement(
              "button",
              {
                onClick: () => {
                  navi.setURL(
                    `gtm:/edit-depend?${[
                      ["id", id],
                      ["type", "dependency"],
                      ["mode", "remove"],
                    ]
                      .map((x) => x.map((y) => escape(y)).join("="))
                      .join("&")}`
                  );
                },
              },
              "Remove"
            )
          )
        ),

        React.createElement(
          "div",
          {
            style: {
              display: "flex",
              flexDirection: "column",
              style: {
                backgroundColor: "rgb(230, 230, 230)",
                marginBottom: "4px",
              },
            },
          }, // Open dependencies
          React.createElement(
            "span",
            { style: { fontSize: "0.8rem", marginBottom: "4px" } },
            "Open"
          ),
          React.createElement(
            "div",
            {
              style: {
                display: "flex",
                flexDirection: "row",
                flexWrap: "wrap",
                minHeight: "4px",
              },
            },
            dependencies_open.map((dep_id) => {
              const task = gtm.task.getTask(dep_id);

              const n = 20;

              const description_short =
                task.description.length <= n
                  ? task.description.substring(0, n)
                  : task.description.substring(0, n - 3) + "...";

              const button_text = `${dep_id}: ${description_short}`;

              return React.createElement(
                "button",
                {
                  key: dep_id,
                  onClick: () => {
                    console.info(`Goto: gtm:/task/${dep_id}`);
                    navi.setURL(`gtm:/task/${dep_id}`);
                  },
                },
                button_text
              );
            })
          )
        ),

        // Non-open dependencies

        React.createElement(
          "div",
          {
            style: {
              display: "flex",
              flexDirection: "column",
            },
          },
          React.createElement(
            "span",
            { style: { fontSize: "0.8rem", marginBottom: "4px" } },
            "Non-open"
          ),
          React.createElement(
            "div",
            {
              style: {
                display: "flex",
                flexDirection: "row",
                flexWrap: "wrap",
              },
            },
            dependencies_nonopen.map((dep_id) => {
              const task = gtm.task.getTask(dep_id);

              const n = 20;

              const description_short =
                task.description.length <= n
                  ? task.description.substring(0, n)
                  : task.description.substring(0, n - 3) + "...";

              const button_text = `${dep_id}: ${description_short}`;

              return React.createElement(
                "button",
                {
                  key: dep_id,
                  onClick: () => {
                    navi.setURL(`gtm:/task/${dep_id}`);
                  },
                },
                button_text
              );
            })
          )
        )
      ),

      <div style={{ display: "flex", flexDirection: "column", marginTop: "8px", marginBottom: "8px", backgroundColor: "#f0f0f0", paddingTop: "4px", paddingBottom: "4px" }}>
        Due date
        {
          !gtm.task.hasDueDate(id)
          ?
          <button onClick={() => {
            gtm.task.changeDueDate(id, (new Date()).toJSON());
            this.forceStatefulRerender();
          }}>Set due date</button>
          :
          <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between", alignItems: "center" }}>
            <button onClick={() => {
              navi.setURL(`gtm:/date/task/${id}/date_due`);
            }}>{ gtm.task.getDueDate(id) }</button>
            <button onClick={() => {
              gtm.task.removeDueDate(id);
              this.forceStatefulRerender();
            }}>Remove</button>
          </div>
        }
      </div>,

      // -----------------------------------------------------------------------
      // Dates

      // Date done
      gtm.task.isDone(id)
        ? React.createElement(
          "div",
          { style: { border: "1px solid var(--missing)" } },
          `Date done: ${gtm.task.getDateDone(id)}`
        )
        : null,
      // Date removed

      gtm.task.isRemoved(id)
        ? React.createElement(
          "div",
          { style: { border: "1px solid var(--missing)" } },
          `Date removed: ${gtm.task.getDateRemoved(id)}`
        )
        : null,

      gtm.task.isArchived(id)
        ? React.createElement(
          "div",
          { style: { border: "1px solid var(--missing)" } },
          `Date archived: ${gtm.task.getDateArchived(id)}`
        )
        : null,

      
      gtm.task.hasDueDate(id)
        ? React.createElement(
          "div",
          { style: { border: "1px solid var(--missing)" } },
          `Date due: ${gtm.task.getDueDate(id)}`
        )
        : null,


      <div style={{ border: "1px solid var(--missing)" }}>
        Estimated time:
        {
          !gtm.task.hasEstimatedTime(id)
          ?
          <button onClick={() => {
            // TODO: finish implementation
            gtm.task.changeEstimatedTime(id, 0);
            this.forceStatefulRerender();
          }}>Set estimated time</button>
          :
          <input type="number" value={gtm.task.getEstimatedTime(id) as number} onChange={(e) => {
            
            const value = parseInt(e.target.value, 10);
            if (Number.isFinite(value)) {

              gtm.task.changeEstimatedTime(id, value);
              this.forceStatefulRerender();
            }
          }}/>
        }
      </div>,

      // -----------------------------------------------------------------------
      // Priority

      <div style={{
        border: "1px solid var(--missing)",
        marginTop: "8px",
        marginBottom: "8px"
      }}>
        Priority
        <input type="number" value={task.priority} onChange={(e) => { console.info(e); }} />
      </div>,

      // -----------------------------------------------------------------------
      // Status
      React.createElement(
        "div",
        { style: { border: "1px solid var(--missing)" } },
        `Status: ${task.status}`
      ),

      // -----------------------------------------------------------------------
      // Work time
      <div style={{ 
        display: "flex",
        flexDirection: "column",
        border: "1px solid var(--missing)",
        marginTop: "8px",
        marginBottom: "8px"
      }}>
        Work time:
        <ul style={{ fontSize: "0.75rem" }}>
          {task.worktime.map((worktime_interval, idx) => {

            if (worktime_interval.length === 2) {
              const [jd_from, jd_to] = worktime_interval;
              return (
                <div key={idx}>{jd_from} - {jd_to}</div>
              );
            } else if (worktime_interval.length === 1) {
              const [jd_from] = worktime_interval;
              return (
                <div key={idx}>{jd_from} -</div>
              );
            } else {
              throw new Error();
            }
          })}
        </ul>
      </div>,
      <div style={{ display: "flex", flexDirection: "column", border: "1px solid var(--missing)" }}>
        Comments:
        {task.comments.map((comment_id, idx2) => {
          const comment = gtm.note.getNote(comment_id);
          const text = comment.text;
          return (
            <textarea key={idx2} value={text} rows={10} onChange={(e: any) => { this.handleCommentChange(e.target.value, idx2); }}></textarea>
          );
          }
        )}
        <button
          onClick={() => {
            gtm.task.addComment(id);
            this.forceStatefulRerender();
          }}
        >
          Add comment
        </button>
      </div>
    );

    
    
    

    const footer = (
      <div key={3} className="bottomBar" style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>
        <div>
          <button onClick={() => {
            if (is_open) gtm.task.finishTask(id);
            else gtm.task.openTask(id);
            this.forceStatefulRerender();
          }}>{is_open ? "Finish" : "Re-open"}</button>
        </div>
        <div>
          <PlayButton
            active={gtm.task.isTaskActive(id)}
            onChange={(active) => {

              if (active) {
                gtm.task.stopAllTasks();
                gtm.task.startTask(id);
                const _id = setInterval(this.animationCallback, 20);
                this.setState({ animate: true, animate_id: _id });
              } else {
                gtm.task.stopAllTasks();
                this.setState({ animate: false, animate_id: null });
              }
            }}
          />
        </div>
      </div>
    );

    return (
      <HCFLayout>
        {header}
        {content}
        {footer}
      </HCFLayout>
    );
  }
}
