// given task.

// TODO: make sure to set this to `false`!
const EXPERIMENTAL = false;

const ALLOWED_STATUSES: GTMEntityTask["status"][] = ["open", "archived", "removed", "done"];

export function _verifyDependencyGraph(state: GTMState) {
  
  // m
  // 0: unmarked
  // 1: temporary mark
  // 2: permanent mark

  const UNMARKED = 2;
  const TEMPORARY_MARK = 3;
  const PERMANENT_MARK = 4;

  const task_ids = Object.keys(state.entities).filter((id) => {
    if (state.entities[id].type === "task") {
      return true;
    } else {
      return false;
    }
  });

  const graph: { [id: string]: { id: string, mark: typeof UNMARKED | typeof TEMPORARY_MARK | typeof PERMANENT_MARK, out: string[] } } = {};

  task_ids.forEach((task_id) => {
    const task = getTask(state, task_id);
    graph[task_id] = {
      // id
      id: task_id,
      // mark
      mark: UNMARKED,
      // out nodes
      out: [...task.dependencies],
    };
  });

  // ---------------------------------------------------------------------
  // Topological sorting: depth-first search

  const L: string[] = [];

  let ok = true;

  // i is node ID
  function visit(id: string) {
    if (!ok) return;

    if (graph[id].mark === PERMANENT_MARK) return;

    if (graph[id].mark === TEMPORARY_MARK) {
      // NOT A DAG
      ok = false;
      return;
    }

    graph[id].mark = TEMPORARY_MARK;
    for (let j = 0; j < graph[id].out.length && ok; j++) {
      visit(graph[id].out[j]);
    }
    graph[id].mark = PERMANENT_MARK;
    L.push(id);
  }

  for (let i = 0; i < task_ids.length && ok; i++) {
    const node_id = task_ids[i];
    if (graph[node_id].mark === UNMARKED) {
      visit(node_id);
    }
  }

  // L is now the nodes of graph topologically sorted :)
  if (ok) L.reverse();

  // ---------------------------------------------------------------------

  return ok;
}

export function _updateTask(state: GTMState, id: string) {
  const task = getTask(state, id);
  const now_json = (new Date()).toJSON();
  task.date_modified = now_json;
}


/**
 * Checks whether these exists a path from `node_id_1` to `node_id_2` in the
 * dependencies.
 *
 * Let `node_id_1` be U and `node_id_2` be V.  Before adding the dependency
 * U -> V (U depends on V), check if there already exists a path from V to U,
 * if so then it will introduce a cycle (NOT ALLOWED IN DAGs), otherwise it
 * is fine.
 *
 * @param {string} node_id_from
 * @param {string} node_id_to
 *
 * @throws Will throw an error if node IDs `node_id_1` and `node_id_2` are not
 * found in the graph.
 */
export function doesPathThroughDependenciesExist(state: GTMState, task_from_id: string, task_to_id: string) {
  // Check if path exists from `node_id_1` to `node_id_2`.
  assert(typeof task_from_id === "string");
  assert(typeof task_to_id === "string");

  assert(isTask(state, task_from_id));
  assert(isTask(state, task_to_id));

  // Depth-first search
  const S = new Set<string>();
  const Q = [task_from_id];

  while (Q.length > 0) {
    const nid = Q.pop() as string;
    if (nid === task_to_id) return true;
    S.add(nid);
    const t = getTask(state, nid);
    const d = t.dependencies;
    for (let i = 0; i < d.length; i++) if (!S.has(d[i])) Q.push(d[i]);
  }

  return false;
}

/**
 * Creates a new task
 * 
 * @param state 
 */
export function createTask(state: GTMState) {
  const id = lib.common.getNextID(state);

  const now_json = new Date().toJSON();

  const task: GTMEntityTask = {
    type: "task",
    id: id,
    title: "",
    description: "",
    date_created: now_json,
    date_modified: now_json,
    date_user_modified: now_json,
    date_due: null,
    date_opened: now_json,
    date_done: null,
    date_archived: null,
    date_removed: null,
    estimated_time: null,
    dependencies: [],
    dependents: [],
    tags: [],
    priority: 0,
    status: "open",
    worktime: [],
    comments: [],
  };

  lib.common.addEntity(state, task);

  return task;
}

export function reorderDependents(state: GTMState, task_id: string, source_index: number, target_index: number) {
  const task = getTask(state, task_id);

  const list = task.dependents;

  assert(list.length > 0);
  assert(Number.isSafeInteger(source_index) && source_index >= 0 && source_index < list.length);
  assert(Number.isSafeInteger(target_index) && target_index >= 0 && target_index < list.length);

  const [removed] = list.splice(source_index, 1);
  list.splice(target_index, 0, removed);

  _updateTask(state, task_id);
}

export function reorderDependencies(state: GTMState, task_id: string, source_index: number, target_index: number) {
  const task = getTask(state, task_id);

  const list = task.dependencies;

  assert(list.length > 0);
  assert(Number.isSafeInteger(source_index) && source_index >= 0 && source_index < list.length);
  assert(Number.isSafeInteger(target_index) && target_index >= 0 && target_index < list.length);

  const [removed] = list.splice(source_index, 1);
  list.splice(target_index, 0, removed);

  _updateTask(state, task_id);
}


/**
 * Makes task with ID `task_from_id` dependent on task with ID `task_to_id`.
 * 
 * @param perm_storage 
 * @param task_from_id 
 * @param task_to_id 
 */
export function addDependency(state: GTMState, task_from_id: string, task_to_id: string) {
  const task_from = getTask(state, task_from_id);
  const task_to = getTask(state, task_to_id);

  const node_id_1 = task_from_id;
  const node_id_2 = task_to_id;

  assert(
    !doesPathThroughDependenciesExist(state, node_id_2, node_id_1),
    `Adding a dependency from node ${node_id_1} to ${node_id_2} will result in a cycle.`
  );

  const node_1 = task_from;
  const node_2 = task_to;

  assert(
    !node_1.dependencies.includes(node_id_2),
    "Node 1 already has node 2 as a dependency."
  );
  assert(
    !node_2.dependents.includes(node_id_1),
    "Node 2 already has node 1 as a dependency."
  );

  node_1.dependencies.push(node_id_2);
  
  if (EXPERIMENTAL) {
    if (Math.random() > 0.5) {
      throw new Error();
    }
  }
  
  node_2.dependents.push(node_id_1);

  


  _updateTask(state, node_id_1);
  _updateTask(state, node_id_2);

  const ok = _verifyDependencyGraph(state);


  if (!ok) {
    // UNDO
    node_1.dependencies.pop();
    node_2.dependents.pop();
    throw new Error("SHOULD NOT HAPPEN");
  }
}

/**
 * Adds `task_2_id` as a dependent for `task_1_id`.
 * 
 * @param perm_storage 
 * @param task_1_id 
 * @param task_2_id 
 */
export function addDependent(state: GTMState, task_1_id: string, task_2_id: string) {
  addDependency(state, task_2_id, task_1_id);
}

/**
 * Removes NODE_ID_2 as a dependency from NODE_ID_1
 * 
 * @param perm_storage 
 * @param node_id_1 
 * @param node_id_2 
 */
export function removeDependency(state: GTMState, node_id_1: string, node_id_2: string) {
  const node_1 = getTask(state, node_id_1);
  const node_2 = getTask(state, node_id_2)

  const idx1 = node_1.dependencies.indexOf(node_id_2);
  const idx2 = node_2.dependents.indexOf(node_id_1);

  assert(idx1 >= 0);
  assert(idx2 >= 0);

  node_1.dependencies.splice(idx1, 1);
  node_2.dependents.splice(idx2, 1);

  assert(!node_1.dependencies.includes(node_id_2));
  assert(!node_2.dependents.includes(node_id_1));

  _updateTask(state, node_id_1);
  _updateTask(state, node_id_2);
}

/**
 * Removes NODE_ID_2 as a dependent from NODE_ID_1
 * 
 * @param perm_storage 
 * @param node_id_1 
 * @param node_id_2 
 */
export function removeDependent(state: GTMState, task_1_id: string, task_2_id: string) {
  removeDependency(state, task_2_id, task_1_id);
}

