/**
 * 
 * @param perm_storage 
 * @param task_id 
 * @param comment 
 * 
 * @throws Raises exception if task doesn't exist.
 */
export function addComment(state: GTMState, task_id: string, comment = "") {
  const task = getTask(state, task_id);

  const note = lib.note.createNote(state, comment);
  
  task.comments.push(note.id);

  _updateTask(state, task_id);
}

export function getComment(state: GTMState, task_id: string, comment_index: number) {
  const task = getTask(state, task_id);

  assert(Number.isSafeInteger(comment_index));
  assert(comment_index >= 0 && comment_index < task.comments.length);

  const comment_id = task.comments[comment_index];

  const note = lib.note.getNote(state, comment_id);

  const comment = note.text;

  return comment;
}

