/**
 * 
 * TODO: check logic
 * 
 * @param perm_storage 
 * @param task_id 
 */
export function getPossibleDependencyIDs(state: GTMState, task_id: string) {
  const task = getTask(state, task_id);

  const possible_dependencies: string[] = [];

  Object.keys(state.entities).forEach((entity_id) => {
    if (task_id !== entity_id && !task.dependencies.includes(entity_id)) {

      // Check if that node is my ancestor (then it's impossible to add)
      if (isTask(state, entity_id) && !lib.task.doesPathThroughDependenciesExist(state, entity_id, task_id)) {
        possible_dependencies.push(entity_id);
      }
    }
  });

  if (true) {
    // Final sanity check
    assert(Array.isArray(possible_dependencies));
    possible_dependencies.forEach((did) => {
      assert(typeof did === "string");
      assert(hasOwnProperty(state.entities, task_id));
    });
  }

  return possible_dependencies;
}

/**
 * 
 * TODO: check logic
 * 
 * @param perm_storage 
 * @param task_id 
 */
export function getPossibleDependentIDs(state: GTMState, task_id: string) {
  
  const task = getTask(state, task_id);

  const possible_dependents: string[] = [];

  Object.keys(state.entities).forEach((entity_id) => {
    if (task_id !== entity_id && !task.dependents.includes(entity_id)) {
      // Check if that node is my ancestor (then it's impossible to add)
      if (isTask(state, entity_id) && !lib.task.doesPathThroughDependenciesExist(state, task_id, entity_id)) {
        possible_dependents.push(entity_id);
      }
    }
  });

  if (true) {
    // Final sanity check
    assert(Array.isArray(possible_dependents));
    possible_dependents.forEach((did) => {
      assert(typeof did === "string");
      assert(hasOwnProperty(state.entities, task_id));
    });
  }

  return possible_dependents;
}

