type ListTaskCriterionFilter = ["filter", ...any[]];

type ListTaskCriterionSort = ["sort", ...any[]];

type ListTaskCriterion = ListTaskCriterionFilter | ListTaskCriterionSort;

type ListTaskCriteria = ListTaskCriterion[];


/**
 * TODO: Add some point reimplement filters and sorts
 */
export function listTaskIDs(state: GTMState, criteria?: ListTaskCriteria) {
  
  // Get all tasks
  let tasks: GTMEntityTask[] = [];
  Object.keys(state.entities).forEach((entity_id) => {
    const entity = state.entities[entity_id];
    if (entity.type === "task") {
      tasks.push(entity);
    }
  });

  if (typeof criteria !== "undefined") {
    assert(Array.isArray(criteria));
    for (let i = 0; i < criteria.length; i++) {
      const criterion = criteria[i];
      assert(Array.isArray(criterion) && criterion.length >= 1);

      const criterion_type = criterion[0];
      // ["filter", ...] or ["sort", ...]

      if (criterion_type === "filter") {
        for (let j = 1; j < criterion.length; j++) {
          // [...]
          const filter: any[] = criterion[j];
          assert(Array.isArray(filter) && filter.length >= 1);

          const filter_type = filter[0];

          if (filter_type === "roots") {
            tasks = tasks.filter((task) => {
              return task.dependents.length === 0;
            });
          } else if (filter_type === "leaves") {
            tasks = tasks.filter((task) => {
              return task.dependencies.length === 0;
            });
          } else if (filter_type === "possible-new-dependencies-of") {
            // ["possible-new-dependencies-of", TASK_ID]
            assert(filter.length === 2);
            const id = filter[1];
            assert(typeof id === "string");
            const ids = getPossibleDependencyIDs(state, id);
            tasks = tasks.filter((task) => {
              return ids.includes(task.id);
            });
          } else if (filter_type === "dependencies-of") {
            // ["dependencies-of", TASK_ID]
            assert(filter.length === 2);
            const id = filter[1];
            assert(typeof id === "string");
            const ids = getTaskDependencyIDs(state, id);
            tasks = tasks.filter((task) => {
              return ids.includes(task.id);
            });
          } else if (filter_type === "possible-new-dependents-of") {
            // ["possible-new-dependents-of", TASK_ID]
            assert(filter.length === 2);
            const id = filter[1];
            assert(typeof id === "string");
            const ids = getPossibleDependentIDs(state, id);
            tasks = tasks.filter((task) => {
              return ids.includes(task.id);
            });
          } else if (filter_type === "dependents-of") {
            // ["dependents-of", TASK_ID]
            assert(filter.length === 2);
            const id = filter[1];
            assert(typeof id === "string");
            const ids = getTaskDependentIDs(state, id);
            tasks = tasks.filter((task) => {
              return ids.includes(task.id);
            });
          } else {
            throw new Error("Unknown filter: " + filter_type);
          }
        }
      } else if (criterion_type === "sort") {
        for (let j = 1; j < criterion.length; j++) {
          // [...]
          const sort: any[] = criterion[j];
          assert(Array.isArray(sort) && sort.length >= 1);

          const sort_type = sort[0];

          if (sort_type === "status") {
            // ["status", "open"]
            // ["status", "open", "closed"]

            const c = sort[sort.length - 1] === "asc" || sort[sort.length - 1] === "desc";
            let n = sort.length;
            if (c) {
              n = sort.length - 1;
            }

            const dedup = new Set<string>();

            for (let k = 1; k < n; k++) {
              assert(ALLOWED_STATUSES.includes(sort[k]));
              assert(!dedup.has(sort[k]));
              dedup.add(sort[k]);
            }

            tasks.sort((a, b) => {
              const a_i = sort.indexOf(a.status);
              const b_i = sort.indexOf(b.status);

              return a_i - b_i;
            });

            if (c) {
              if (sort[sort.length - 1] === "desc") {
                tasks.reverse();
              }
            }
          } else if (sort_type === "date_modified") {
            // ["date_modified"]
            // ["date_modified", "asc"]
            // ["date_modified", "desc"]
            tasks.sort((a, b) => {
              const t_a = (new Date(a.date_modified)).getTime();
              const t_b = (new Date(b.date_modified)).getTime();
              return t_a - t_b;
            });

            if (sort.length === 2) {
              assert(sort[1] === "asc" || sort[1] === "desc");
              if (sort[1] === "desc") {
                tasks.reverse();
              }
            }
          } else {
            throw new Error("Unknown sort: " + sort_type);
          }
        }
      } else {
        throw new Error("Unknown criterion: " + criterion_type);
      }
    }
  }

  const task_ids = tasks.map((task) => { 
    return task.id;
  });

  return task_ids;
}

