export function getTaskSelfWorktime(state: GTMState, task_id: string) {
  const task = getTask(state, task_id);

  const worktime = task.worktime;

  let duration = 0;

  const json_now = (new Date()).toJSON();

  for (let i = 0; i < worktime.length; i++) {
    const wtu = worktime[i];
    assert(wtu.length >= 1 && wtu.length <= 2);
    let jt1, jt2;
    if (wtu.length === 1) {
      jt1 = wtu[0];
      jt2 = json_now;
    } else if (wtu.length === 2) {
      jt1 = wtu[0];
      jt2 = wtu[1];
    } else {
      throw new Error();
    }

    assert(typeof jt1 === "string", JSON.stringify(worktime));
    assert(typeof jt2 === "string", JSON.stringify(worktime));
    duration += getDurationISO8601InMS(jt1, jt2);
  }

  return duration;
}

export function getTotalDependencyGraph(state: GTMState, task_id: string) {
  assert(isTask(state, task_id));

  const D = new Set<string>();
  const Q = [task_id];

  while (Q.length > 0) {
    const tid = Q.shift() as string; // USE POP!
    D.add(tid);

    const task = getTask(state, tid);
    task.dependencies.forEach((dependency) => {
      Q.push(dependency);
    });
  }

  // One cannot be one's own dependency.
  D.delete(task_id);

  return Array.from(D);
}

export function getTaskTotalWorktime(state: GTMState, task_id: string) {
  assert(isTask(state, task_id));
  let duration = 0;  
  duration += getTaskSelfWorktime(state, task_id);
  
  const d_ids = getTotalDependencyGraph(state, task_id);
  d_ids.forEach((d_id) => {
    duration += getTaskSelfWorktime(state, d_id);
  });
  return duration;
}

