export function finishTask(state: GTMState, task_id: string) {
  const task = getTask(state, task_id);
  assert(task.status !== "done");
  if (isTaskActive(state, task_id)) stopTask(state, task_id);
  changeStatus(state, task_id, "done");
  _updateTask(state, task_id);
}

/**
 * Replaces `reopen_task`.
 * 
 * @param perm_storage 
 */
export function openTask(state: GTMState, task_id: string) {
  const task = getTask(state, task_id);
  assert(task.status !== "open");
  changeStatus(state, task_id, "open");
}

export function deleteTask(state: GTMState, task_id: string) {
  assert(isTask(state, task_id));

  const task = getTask(state, task_id);

  // ---------------------------------------------------------------------
  // If the task is running, then stop it.

  if (isTaskActive(state, task_id)) {
    stopTask(state, task_id);
  }

  // ---------------------------------------------------------------------
  // Remove all dependencies
  
  task.dependencies.forEach((dep_id) => {
    removeDependency(state, task_id, dep_id);
  });

  // ---------------------------------------------------------------------
  // Remove all dependents
  task.dependents.forEach((dep_id) => {
    removeDependent(state, task_id, dep_id);
  });

  // ---------------------------------------------------------------------
  // Delete task
  lib.common.removeEntity(state, task_id);

  // ---------------------------------------------------------------------
  // Checks

  const is_ok = _verifyDependencyGraph(state);
  if (!is_ok) {
    throw new Error("Not ok!");
  }
}

export function isTaskActive(state: GTMState, task_id: string) {
  return state.misc.active_task_ids.includes(task_id);
}

export function startTask(state: GTMState, task_id: string) {
  const task = getTask(state, task_id);

  // Make sure that the task is not tracked as active.
  assert(!state.misc.active_task_ids.includes(task_id));

  // Task has to be "open" so you can start it.
  assert(task.status === "open");

  state.misc.active_task_ids.push(task_id);

  const now_json = new Date().toJSON();

  task.worktime.push([now_json]);

  _updateTask(state, task_id);
}

export function stopTask(state: GTMState, task_id: string) {
  const task = getTask(state, task_id);

  assert(task.status === "open");

  const index = state.misc.active_task_ids.indexOf(task_id);
  assert(state.misc.active_task_ids.includes(task_id));

  

  assert(task.worktime.length > 0);
  assert(task.worktime[task.worktime.length - 1].length === 1);

  task.worktime[task.worktime.length - 1].push(new Date().toJSON());

  _updateTask(state, task_id);

  state.misc.active_task_ids.splice(index, 1);
}

export function stopAllTasks(state: GTMState) {
  state.misc.active_task_ids.forEach((task_id) => {
    stopTask(state, task_id);
  });
}

