/**
 * This function should NOT be exported, it should ONLY be called within this  
 * module.
 * 
 * To change the status of a task you should do it through the functions,
 * * `openTask`
 * * `finishTask`
 * * `archiveTask`
 * * `removeTask`
 * 
 * If the status is changed to the same status, an error is raised.
 * 
 * @param state 
 * @param id 
 * @param status 
 */
function changeStatus(state: GTMState, id: string, status: GTMEntityTask["status"]) {
  const task = getTask(state, id);

  assert(ALLOWED_STATUSES.includes(status));
  const current_status = task.status;
  assert(status !== current_status);

  const json_now = (new Date()).toJSON();

  if (current_status === "open") {
    if (status === "done") task.date_done = json_now;
    else if (status === "removed") task.date_removed = json_now;
    else if (status === "archived") task.date_archived = json_now;
    else throw new Error("Unknown status: " + status);

    task.date_opened = null;
    task.status = status;
  } else if (current_status === "done") {
    if (status === "open") task.date_opened = json_now;
    else if (status === "removed") task.date_removed = json_now;
    else if (status === "archived") task.date_archived = json_now;
    else throw new Error("Unknown status: " + status);

    task.date_done = null;
    task.status = status;
  } else if (current_status === "removed") {
    if (status === "open") task.date_opened = json_now;
    else if (status === "done") task.date_done = json_now;
    else if (status === "archived") task.date_archived = json_now;
    else throw new Error("Unknown status: " + status);

    task.date_opened = null;
    task.status = status;
  } else if (current_status === "archived") {
    if (status === "open") task.date_opened = json_now;
    else if (status === "done") task.date_done = json_now;
    else if (status === "removed") task.date_removed = json_now;
    else throw new Error("Unknown status: " + status);

    task.date_archived = null;
    task.status = status;
  } else throw new Error("Unknown current status: " + current_status);

  _updateTask(state, id);
}

