/**
 * @param state 
 * @param id 
 * @throws If the entity with ID `task_id` is not of type `task` then an exception is raised.
 */
export function isTask(state: GTMState, id: string) {
  if (!hasOwnProperty(state.entities, id)) return false;
  const entity = state.entities[id];
  if (entity.type === "task") return true;
  else return false;
}

/**
 * @param state 
 * @param id 
 * @throws If the entity with ID `task_id` is not of type `task` then an exception is raised.
 */
export function hasTask(state: GTMState, id: string) {
  if (!hasOwnProperty(state.entities, id)) return false;
  const entity = state.entities[id];
  if (entity.type === "task") return true;
  else return false;
}

/**
 * Retrieves the task with ID `id` in state `state`.
 * 
 * If an entity with that ID doesn't exist or that entity exist but is not of
 * type `"task"` then an exception is raised.
 * 
 * @param state 
 * @param id 
 * @throws throws error if task doesn't exist with given ID
 */
export function getTask(state: GTMState, id: string) {
  assert(hasOwnProperty(state.entities, id));
  const entity = state.entities[id];
  assert(entity.type === "task");
  return entity;
}

