import React, { useState, useEffect } from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";


import HCFLayout from "../../layout/hcf";
import { Navi } from '../../navi';
import { GTMApp } from "../../gtm-app/gtm";

import TaskListItem from "../../component/task-list-item";

import "./index.css";


interface IProps {
  gtm: GTMApp,
  navi: Navi
}

const reorder = (list: any[], startIndex: number, endIndex: number) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};


/**
 * URL: `gtm:/test4`
 * 
 * @param props 
 */
const PageTest4: React.FC<IProps> = (props) => {
  const { navi, gtm } = props;

  const { id: task_id } = navi.urlObj.queryObject;

  function getIDs(task_id: string) {
    let initial_supertask_ids: string[] = [];
    let initial_subtask_ids: string[] = [];

    if (typeof task_id === "undefined") {
      // Roots
      initial_supertask_ids = [];
      initial_subtask_ids = gtm.task.listTaskIDs([
        ["filter", ["roots"]],
        ["sort", ["status", "open", "asc"], ["date_modified", "desc"]],
      ]);
    } else {

      if (gtm.task.hasTask(task_id)) {
        const task = gtm.task.getTask(task_id);

        if (task.dependents.length === 0) {
          // Roots
          initial_supertask_ids = gtm.task.listTaskIDs([
            ["filter", ["roots"]],
            ["sort", ["status", "open", "asc"], ["date_modified", "desc"]],
          ]).filter(id => id !== task_id);
        } else {
          // Dependents
          initial_supertask_ids = [...task.dependents];
        }

        initial_subtask_ids = [...task.dependencies];
      } else {
        // error
      }

    }

    return [initial_supertask_ids, initial_subtask_ids];
  }

  const [initial_supertask_ids, initial_subtask_ids] = getIDs(task_id);

  const [state, setState] = useState<{ supertask_ids: string[], subtask_ids: string[], task_id?: string }>({
    supertask_ids: initial_supertask_ids,
    subtask_ids: initial_subtask_ids,
    task_id,
  });

  useEffect(() => {
    if (task_id !== state.task_id) {
      const [initial_supertask_ids, initial_subtask_ids] = getIDs(task_id);
      setState({
        ...state,
        supertask_ids: initial_supertask_ids,
        subtask_ids: initial_subtask_ids,
        task_id,
      })
    }
  }, [task_id]);


  if (typeof task_id !== "undefined") {
    if (!gtm.task.hasTask(task_id)) {
      navi.back();
      return (
        <div>...</div>
      );
    }
  }

  let the_task = typeof task_id === "undefined" ? null : gtm.task.getTask(task_id);

  let description: string;

  if (the_task === null) {
    description = "Roots";
  } else {
    if (the_task.description.length > 20) {
      description = the_task.description.substring(0, 17) + "...";
    } else {
      description = the_task.description;
    }
  }



  const header = (
    <div key={1} className="topBar"
      style={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between"
      }}
    >
      <div>
        <button
          onClick={() => {
            navi.back();
          }}
        >
          Go back
        </button>
        { description }
      </div>
      <div>
        <button
          onClick={() => {
            navi.setURL("gtm:/home");
          }}
        >
          Go home
      </button>
      </div>
    </div>
  );

  let content;

  if (typeof task_id === "undefined") {
    // Show root elements
    content = (
      <div key={2}>
        <DragDropContext
          onDragEnd={(result) => {
            if (!result.destination) {
              return;
            }

            if (result.destination.index === result.source.index) {
              return;
            }

            const subtask_ids = reorder(
              state.subtask_ids,
              result.source.index,
              result.destination.index
            );

            setState({ ...state, subtask_ids });
          }}
        >
          <Droppable droppableId="list">
            {provided => (
              <div ref={provided.innerRef} {...provided.droppableProps}>
                {state.subtask_ids.map((subtask_id, index) => {
                  return (
                    <Draggable key={subtask_id} draggableId={subtask_id} index={index}>
                      {provided => (
                        <div
                          className="page-test-item"
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                        >
                          <TaskListItem
                            gtm={gtm}
                            navi={navi}
                            task_id={subtask_id}
                            buttonEnterText=">"
                            buttonEnterOnClick={() => {
                              navi.setURL(`gtm:/test4?id=${subtask_id}`);
                            }}
                          />
                        </div>
                      )}
                    </Draggable>
                  );
                })}
                {provided.placeholder}
              </div>
            )}
          </Droppable>
        </DragDropContext>
      </div>
    );
  } else {
    const task = gtm.task.getTask(task_id);
    if (task.dependents.length === 0) {
      // Has no dependents
      content = (
        <div key={2}>
          <div style={{ display: "flex", flexDirection: "column" }}>
            <button
              onClick={() => {
                navi.setURL(`gtm:/test4`);
              }}
            >Roots</button>
          </div>
          Task
          <TaskListItem
            gtm={gtm}
            navi={navi}
            task_id={task_id}
            buttonEnterHide={true}
          />
          Subtasks
          <DragDropContext
            onDragEnd={(result) => {
              if (!result.destination) {
                return;
              }

              if (result.destination.index === result.source.index) {
                return;
              }

              gtm.task.reorderDependencies(task_id, result.source.index, result.destination.index);

              const subtask_ids = reorder(
                state.subtask_ids,
                result.source.index,
                result.destination.index
              );

              setState({ ...state, subtask_ids });
            }}
          >
            <Droppable droppableId="list2">
              {provided => (
                <div ref={provided.innerRef} {...provided.droppableProps}>
                  {state.subtask_ids.map((subtask_id, index) => {
                    return (
                      <Draggable key={subtask_id} draggableId={subtask_id} index={index}>
                        {provided => (
                          <div
                            className="page-test-item"
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                          >
                            <TaskListItem
                              gtm={gtm}
                              navi={navi}
                              task_id={subtask_id}
                              buttonEnterText=">"
                              buttonEnterOnClick={() => {
                                navi.setURL(`gtm:/test4?id=${subtask_id}`);
                              }}
                            />
                          </div>
                        )}
                      </Draggable>
                    );
                  })}
                  {provided.placeholder}
                </div>
              )}
            </Droppable>
          </DragDropContext>
        </div>
      );
    } else {
      // Has dependents
      content = (
        <div key={2}>
          Supertasks

            <DragDropContext
            onDragEnd={(result) => {
              if (!result.destination) {
                return;
              }

              if (result.destination.index === result.source.index) {
                return;
              }

              gtm.task.reorderDependents(task_id, result.source.index, result.destination.index);

              const supertask_ids = reorder(
                state.supertask_ids,
                result.source.index,
                result.destination.index
              );
              setState({ ...state, supertask_ids });
            }}
          >
            <Droppable droppableId="list1">
              {provided => (
                <div ref={provided.innerRef} {...provided.droppableProps}>
                  {state.supertask_ids.map((supertask_id, index) => {
                    return (
                      <Draggable key={supertask_id} draggableId={supertask_id} index={index}>
                        {provided => (
                          <div
                            className="page-test-item"
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                          >
                            <TaskListItem
                              gtm={gtm}
                              navi={navi}
                              task_id={supertask_id}
                              buttonEnterText="<"
                              buttonEnterOnClick={() => {
                                navi.setURL(`gtm:/test4?id=${supertask_id}`);
                              }}
                            />
                          </div>
                        )}
                      </Draggable>
                    );
                  })}
                  {provided.placeholder}
                </div>
              )}
            </Droppable>
          </DragDropContext>
          Task
          <TaskListItem
            gtm={gtm}
            navi={navi}
            task_id={task_id}
            buttonEnterHide={true}
          />
          Subtasks
          <DragDropContext
            onDragEnd={(result) => {
              if (!result.destination) {
                return;
              }

              if (result.destination.index === result.source.index) {
                return;
              }

              gtm.task.reorderDependencies(task_id, result.source.index, result.destination.index);

              const subtask_ids = reorder(
                state.subtask_ids,
                result.source.index,
                result.destination.index
              );

              setState({ ...state, subtask_ids });
            }}
          >
            <Droppable droppableId="list2">
              {provided => (
                <div ref={provided.innerRef} {...provided.droppableProps}>
                  {state.subtask_ids.map((subtask_id, index) => {
                    return (
                      <Draggable key={subtask_id} draggableId={subtask_id} index={index}>
                        {provided => (
                          <div
                            className="page-test-item"
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                          >
                            <TaskListItem
                              gtm={gtm}
                              navi={navi}
                              task_id={subtask_id}
                              buttonEnterText=">"
                              buttonEnterOnClick={() => {
                                navi.setURL(`gtm:/test4?id=${subtask_id}`);
                              }}
                            />
                          </div>
                        )}
                      </Draggable>
                    );
                  })}
                  {provided.placeholder}
                </div>
              )}
            </Droppable>
          </DragDropContext>
        </div>
      );
    }
  }



  const footer = (
    <div key={3} className="bottomBar" style={{ display: "flex", flexDirection: "row", justifyContent: "flex-end" }}>
      <button
        onClick={() => {
          if (typeof task_id !== "undefined") {
            navi.setURL(
              `gtm:/edit-depend?${[
                ["id", task_id],
                ["type", "dependency"],
                ["mode", "add"],
              ]
                .map((x) => x.map((y) => escape(y)).join("="))
                .join("&")}`
            );
          } else {
            const task = gtm.task.createTask();
            navi.setURL(`gtm:/task/${task.id}`);
          }
        }}
      >
        &nbsp;+&nbsp;
    </button>
    </div>
  );

  return (
    <HCFLayout>
      {header}
      {content}
      {footer}
    </HCFLayout>
  );
}


export default PageTest4;
