import React from "react";

import "./index.css";

interface AnyProps {
  [key: string]: any;
}

interface Props extends AnyProps {
  value: string,
  onChange: (x: React.ChangeEvent<HTMLTextAreaElement>) => void
}

interface State {
  value: string;
}


export default class TextArea extends React.Component<Props, State> {
  /**
   * @param {{value: string, onChange: (e: any) => void}} props
   */
  constructor(props: Props) {
    super(props);

    this.state = {
      value: props.value,
    };

    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event: React.ChangeEvent<HTMLTextAreaElement>) {
    this.setState({ value: event.target.value });
    this.props.onChange(event);
  }

  render() {
    const { value, children } = this.props;
    return <textarea value={value} onChange={this.handleChange} {...this.props} >{children}</textarea>;
  }
}
