/*
Simple Note

Stores title, text and modification dates.
*/

import { GTMEntityNote_v1 } from "../gtm-lib-common";
import { GTMState } from "../gtm-lib-common";
import { assert, hasOwnProperty } from "../utils";
import lib from "../gtm-lib";

export function createNote(state: GTMState, text = "") {
  const id = lib.common.getNextID(state);

  const now_json = (new Date()).toJSON();

  const note: GTMEntityNote_v1 = {
    type: "note",
    id,

    title: "",
    text,

    date_created: now_json,
    date_modified: now_json,
  };

  lib.common.addEntity(state, note);

  return note;
}

export function changeText(state: GTMState, id: string, text: string) {
  const note = getNote(state, id);

  const now_json = (new Date()).toJSON();

  note.text = text;
  note.date_modified = now_json;
}

export function getNote(state: GTMState, id: string) {
  assert(hasOwnProperty(state.entities, id));
  const entity = state.entities[id];
  assert(entity.type === "note");
  return entity;
}

