import React, { useState, useEffect } from 'react';

import PageEditTask from "./page/edit-task";
import PageHome from "./page/home";
import PageEditDepend from "./page/edit-depend";
import PageTaskList from "./page/task-list";
import PageExport from "./page/export";
import PageImport from "./page/import";
import PageDev from "./page/dev";
import PageDevServer from "./page/dev-server";
import PageCreationSelection from "./page/creation-selection";
import PageDate from "./page/date";
import PageGantt from "./page/gantt";
import PageTest4 from "./page/test-4";
import PageAbout from "./page/about";

import MobileLayout from "./layout/mobile";

import { Navi } from './navi';
import { GTMApp } from "./gtm-app/gtm";
import { debounce } from './utils-ui';

import "./App.css";


export type Route = { name: string, url: string };
export type Routes = Route[];


const route_handlers = [
  {
    name: "Developer page: server",
    url: "gtm:/dev/server",
    element: PageDevServer
  },
  {
    name: "Creation selection",
    url: "gtm:/creation-selection",
    element: PageCreationSelection
  },
  {
    name: "Edit depend",
    url: "gtm:/edit-depend",
    element: PageEditDepend,
  },
  {
    name: "Home",
    url: "gtm:/home",
    element: PageHome,
  },
  {
    name: "Task list (legacy)",
    url: "gtm:/tasklist",
    element: PageTaskList,
  },
  {
    name: "Edit task",
    url: "gtm:/task",
    element: PageEditTask,
  },
  {
    name: "Import",
    url: "gtm:/import",
    element: PageImport,
  },
  {
    name: "Export",
    url: "gtm:/export",
    element: PageExport,
  },
  {
    name: "Developer page",
    url: "gtm:/dev",
    element: PageDev,
  },
  {
    name: "Date",
    url: "gtm:/date",
    element: PageDate
  },
  {
    name: "Gantt",
    url: `gtm:/gantt`,
    element: PageGantt
  },
  {
    name: "Task list",
    url: "gtm:/test4",
    element: PageTest4
  },
  {
    name: "About",
    url: "gtm:/about",
    element: PageAbout
  }
];


const child_props_routes: any[] = [];
for (let i = 0; i < route_handlers.length; i++) {
  const route_handler = route_handlers[i];
  const route_object: { [key: string]: any } = {};
  Object.keys(route_handler).forEach((key) => {
    if (key !== "element") {
      route_object[key] = (route_handler as any)[key];
    }
  });
  child_props_routes.push(route_object);
}





type Props = {
  gtm: GTMApp,
  navi: Navi
}

const App: React.FC<Props> = (props) => {
  const { gtm, navi } = props;

  const { url } = navi;

  const [count, setCount] = useState(1);

  const [dimensions, setDimensions] = useState({
    height: window.innerHeight,
    width: window.innerWidth,
  });

  const initial_url = "gtm:/home";

  useEffect(() => {
    function handleResize() {
      if (
        !(
          dimensions.height === window.innerHeight &&
          dimensions.width === window.innerWidth
        )
      ) {
        setDimensions({
          height: window.innerHeight,
          width: window.innerWidth,
        });
      }
    }
    const deboundedHandleResize = debounce(handleResize, 500);
    window.addEventListener("resize", deboundedHandleResize);
    return () => {
      // Cleanup
      window.removeEventListener("resize", deboundedHandleResize);
    };
  });

  navi.callback = () => {
    setCount(count + 1);
  };

  const child_props = {
    navi,
    gtm,
    routes: JSON.parse(JSON.stringify(child_props_routes))
  };

  let found_index = -1;

  for (let i = 0; i < route_handlers.length; i++) {
    const route_handler = route_handlers[i];
    if (url.startsWith(route_handler.url)) {
      found_index = i;
      break;
    }
  }

  let reo;

  if (found_index !== -1) {
    reo = (
      <MobileLayout>
        {React.createElement(route_handlers[found_index].element as any, child_props)}
      </MobileLayout>
    );
  } else {
    reo = (
      <MobileLayout>
        <div className="app">
          Unhandled route: {url}
          <button onClick={() => { navi.setURL(initial_url); }} >Go home</button>
        </div>
      </MobileLayout>
    );
  }

  // If this application breaks then maybe try to reload the page.
  (window as any)["isgtmok"] = true;

  return reo;
}

export default App;
